                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md4>
                  <v-text-field
                    v-model="currentPhysician.practice_location1_state"
                    label="State"
                    :disabled="!isAdministrator"
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md4>
                  <v-text-field
                    v-model="currentPhysician.practice_location1_zip"
                    label="Zip"
                    :disabled="!isAdministrator"
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <p>Additional Address</p>
              <v-layout row wrap>
                <v-flex xs12>
                  <v-text-field
                    v-model="currentPhysician.practice_location2_address1"
                    label="Address1"
                    :disabled="!isAdministrator"
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12>
                  <v-text-field
                    v-model="currentPhysician.practice_location2_address2"
                    label="Address2"
                    :disabled="!isAdministrator"
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md4>
                  <v-text-field
                    v-model="currentPhysician.practice_location2_city"
                    label="City"
                    :disabled="!isAdministrator"
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md4>
                  <v-text-field
                    v-model="currentPhysician.practice_location2_state"
                    label="State"
                    :disabled="!isAdministrator"
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md4>
                  <v-text-field
                    v-model="currentPhysician.practice_location2_zip"
                    label="Zip"
                    :disabled="!isAdministrator"
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12>
                  <v-textarea
                    v-model="currentPhysician.comments"
                    box
                    label="Comments"
                    auto-grow
                  ></v-textarea>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs4>
                  <v-menu
                    ref="menuDatePickerStart"
                    :close-on-content-click="false"
                    v-model="menuDatePickerStart"
                    :nudge-right="40"
                    :return-value.sync="currentPhysician.start_date"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                  >
                    <v-text-field
                      slot="activator"
                      v-model="currentPhysician.start_date"
                      label="Start Date"
                      prepend-icon="event"
                      readonly
                      v-validate="{
                        rules: { required: this.isTermDateRequired },
                      }"
                      data-vv-name="Start Date"
                      :error-messages="errors.collect('Start Date')"
                    ></v-text-field>
                    <v-date-picker
                      v-model="currentPhysician.start_date"
                      @input="
                        $refs.menuDatePickerStart.save(
                          currentPhysician.start_date
                        )
                      "
                    ></v-date-picker>
                  </v-menu>
                </v-flex>
                <v-flex xs4>
                  <v-checkbox
                    v-model="currentPhysician.terminated"
                    label="Terminated"
                    :disabled="!isAdministrator"
                  ></v-checkbox>
                </v-flex>
                <v-flex xs4>
                  <v-menu
                    v-if="this.currentPhysician.terminated"
                    ref="menuDatePicker"
                    :close-on-content-click="false"
                    v-model="menuDatePicker"
                    :nudge-right="40"
                    :return-value.sync="currentPhysician.terminated_date"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                  >
                    <v-text-field
                      slot="activator"
