      </v-card>
    </v-container>
    <PhysicianAudits
      v-if="showPhysicianProfile"
      :npi="selectedNpi"
    ></PhysicianAudits>
    <EducationListing
      v-if="showPhysicianProfile"
      :npi="selectedNpi"
      :startDate="currentPhysician.start_date"
