<template>
  <div>
    <v-container>
      <custom-snackbar ref="customSnackbar"></custom-snackbar>
      <v-card>
        <v-card-title
          class="headline font-weight-regular chsli-color white--text"
          >Audit Record</v-card-title
        >
        <v-card-text>
          <v-form ref="form">
            <v-container fluid>
              <v-subheader>Encounter Details</v-subheader>
              <v-divider></v-divider>
              <v-layout row wrap>
                <v-flex xs12 md2 class="pr-1">
                  <v-text-field
                    v-model="audit.id"
                    label="Audit ID"
                    disabled
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md5 class="pr-1">
                  <v-text-field
                    v-model="auditorName"
                    label="Auditor"
                    disabled
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md3 class="pr-1">
                  <v-menu
                    ref="menuDatePicker"
                    :close-on-content-click="false"
                    v-model="menuDatePicker"
                    :nudge-right="40"
                    :return-value.sync="audit.audit_date"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                  >
                    <v-text-field
                      slot="activator"
                      v-model="audit.audit_date"
                      label="Audit Date"
                      prepend-icon="event"
                      readonly
                      data-vv-name="Audit Date"
                      :error-messages="errors.collect('Audit Date')"
                    ></v-text-field>
                    <v-date-picker
                      v-model="audit.audit_date"
                      @input="$refs.menuDatePicker.save(audit.audit_date)"
                    ></v-date-picker>
                  </v-menu>
                </v-flex>
                <v-flex xs12 md2 class="pr-1">
                  <v-text-field
                    v-model="audit.audit_completion_date"
                    label="Completed Date"
                    disabled
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md4 class="pr-1">
                  <v-text-field
                    :value="currentPhysicianName"
                    label="Provider Name"
                    disabled
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md3 class="pr-1">
                  <v-text-field
                    v-model="audit.npi"
                    label="Provider NPI"
                    disabled
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md5 class="pr-1">
                  <v-text-field
                    v-model="audit.facility"
                    label="Facility"
                    :disabled="!newRecord"
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md5 class="pr-1">
                  <v-text-field
                    v-model="audit.patient_name"
                    label="Patient Name"
                    :disabled="!newRecord"
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md2 class="pr-1">
                  <v-menu
                    ref="menuPtDOB"
                    :close-on-content-click="false"
                    v-model="menuPtDOB"
                    :nudge-right="40"
                    :return-value.sync="audit.date_of_birth"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                    :disabled="!newRecord"
                  >
                    <v-text-field
                      slot="activator"
                      v-model="audit.date_of_birth"
                      :disabled="!newRecord"
                      label="Date of Birth"
                      prepend-icon="event"
                      readonly
                      data-vv-name="Date of Birth"
                      :error-messages="errors.collect('Date of Birth')"
                    ></v-text-field>
                    <v-date-picker
                      v-model="audit.date_of_birth"
                      @input="$refs.menuPtDOB.save(audit.date_of_birth)"
                    ></v-date-picker>
                  </v-menu>
                </v-flex>
                <v-flex xs12 md3 class="pr-1">
                  <v-text-field
                    v-model="audit.payor"
                    label="Payer"
                    :disabled="!newRecord"
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md2 class="pr-1">
                  <v-menu
                    ref="menuPtDOService"
                    :close-on-content-click="false"
                    v-model="menuPtDOService"
                    :nudge-right="40"
                    :return-value.sync="audit.service_date"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                    :disabled="!newRecord"
                  >
                    <v-text-field
                      slot="activator"
                      v-model="audit.service_date"
                      :disabled="!newRecord"
                      label="Service Date"
                      prepend-icon="event"
                      readonly
                      data-vv-name="Service Date"
                      :error-messages="errors.collect('Service Date')"
                    ></v-text-field>
                    <v-date-picker
                      v-model="audit.service_date"
                      @input="$refs.menuPtDOService.save(audit.service_date)"
                    ></v-date-picker>
                  </v-menu>
                </v-flex>
              </v-layout>
              <v-subheader>
                Coding Details
                <v-btn
                  small
                  color="success"
                  class="btn btn-secondary"
                  @click="addDetailSection()"
                  >Add Detail Section</v-btn
                >
              </v-subheader>
              <v-divider></v-divider>
              <v-layout row wrap>
                <v-flex xs12>
                  <div
                    v-for="(detail, index) in audit.details"
                    :key="index"
                    class="grey lighten-5"
                  >
                    <!-- changed key from detail.cpt_billed to index 7.6 -->
                    <v-container mt-2>
                      <v-layout row wrap>
                        <v-flex xs12 md4 class="pr-1">
                          <v-layout row wrap>
                            <v-flex xs12 class="pr-1">
                              <v-text-field
                                v-model="detail.cpt_billed"
                                label="CPT Code Billed"
                                :disabled="!newRecord"
                              ></v-text-field>
                            </v-flex>
                          </v-layout>
                          <v-layout row wrap>
                            <v-flex xs9 class="pr-1">
                              <v-text-field
                                v-model="detail.cpt_audited"
                                label="CPT Code Audited"
                              ></v-text-field>
                            </v-flex>
                            <v-flex xs3>
                              <v-select
                                v-model="detail.cpt_audited_score"
                                :items="scoringList"
                                item-text="comment_code"
                                item-value="comment_code"
                                label="Score"
                                @change="
                                  updateComments(
                                    index,
                                    'cpt_audited_score',
                                    detail.cpt_audited_score
                                  )
                                "
                              ></v-select>
                            </v-flex>
                          </v-layout>
                          <v-layout row wrap>
                            <v-flex xs9 class="pr-1">
                              <v-text-field
                                v-model="detail.service_provider"
                                label="Service Provider"
                                :disabled="!newRecord && !detail.new_row"
                              ></v-text-field>
                            </v-flex>
                            <v-flex xs3>
                              <v-select
                                v-model="detail.service_provider_score"
                                :items="scoringList"
                                item-text="comment_code"
                                item-value="comment_code"
                                label="Score"
                                @change="
                                  updateComments(
                                    index,
                                    'service_provider_score',
                                    detail.service_provider_score
                                  )
                                "
                              ></v-select>
                            </v-flex>
                          </v-layout>
                        </v-flex>
                        <v-flex xs12 md4 class="pr-1">
                          <v-layout row wrap>
                            <v-flex xs12 class="pr-1">
                              <v-text-field
                                v-model="detail.modifier_billed"
                                label="Modifier Billed"
                                :disabled="!newRecord"
                              ></v-text-field>
                            </v-flex>
                          </v-layout>
                          <v-layout row wrap>
                            <v-flex xs9 class="pr-1">
                              <v-text-field
                                v-model="detail.modifier_audited"
                                label="Modifier Audited"
                              ></v-text-field>
                            </v-flex>
                            <v-flex xs3>
                              <v-select
                                v-model="detail.modifier_audited_score"
                                :items="scoringList"
                                item-text="comment_code"
                                item-value="comment_code"
                                label="Score"
                                @change="
                                  updateComments(
                                    index,
                                    'modifier_audited_score',
                                    detail.modifier_audited_score
                                  )
                                "
                              ></v-select>
                            </v-flex>
                          </v-layout>
                          <v-layout row wrap>
                            <v-flex xs9 pr-1>
                              <v-text-field
                                v-model="detail.pos_description"
                                label="POS Description"
                                :disabled="!newRecord && !detail.new_row"
                              ></v-text-field>
                            </v-flex>
                            <v-flex xs3>
                              <v-select
                                v-model="detail.pos_description_score"
                                :items="scoringList"
                                item-text="comment_code"
                                item-value="comment_code"
                                label="Score"
                                @change="
                                  updateComments(
                                    index,
                                    'pos_description_score',
                                    detail.pos_description_score
                                  )
                                "
                              ></v-select>
                            </v-flex>
                          </v-layout>
                        </v-flex>
                        <v-flex xs12 md4 class="pr-1">
                          <v-layout row wrap>
                            <v-flex xs12 class="pr-1">
                              <v-text-field
                                :disabled="!newRecord"
                                v-model="detail.units_billed"
                                label="Units Billed"
                              ></v-text-field>
                            </v-flex>
                          </v-layout>
                          <v-layout row wrap>
                            <v-flex xs9 class="pr-1">
                              <v-text-field
                                v-model="detail.units_audited"
                                label="Units Audited"
                                :name="'Units Audited-' + index"
                                data-vv-validate="'checkUnitsBilled'"
                                :error-messages="
                                  errors.collect(`Units Audited-${index}`)
                                "
                              ></v-text-field>
                            </v-flex>
                            <v-flex xs3>
                              <v-select
                                v-model="detail.units_audited_score"
                                :items="scoringList"
                                item-text="comment_code"
                                item-value="comment_code"
                                label="Score"
                                @change="
                                  updateComments(
                                    index,
                                    'units_audited_score',
                                    detail.units_audited_score
                                  )
                                "
                              ></v-select>
                            </v-flex>
                          </v-layout>
                          <v-layout row wrap>
                            <v-flex xs2 pr-1>
                              <v-text-field
                                v-model="detail.pos_type_id"
                                label="ID"
                                :disabled="!newRecord && !detail.new_row"
                              ></v-text-field>
                            </v-flex>
                            <v-flex xs7 pr-1>
                              <v-text-field
                                v-model="detail.pos_type"
                                label="POS Type"
                                :disabled="!newRecord && !detail.new_row"
                              ></v-text-field>
                            </v-flex>
                            <v-flex xs3>
                              <v-select
                                v-model="detail.pos_type_score"
                                :items="scoringList"
                                item-text="comment_code"
                                item-value="comment_code"
                                label="Score"
                                @change="
                                  updateComments(
                                    index,
                                    'pos_type_score',
                                    detail.pos_type_score
                                  )
                                "
                              ></v-select>
                            </v-flex>
                          </v-layout>
                        </v-flex>
                      </v-layout>
                      <v-layout row wrap class="mt-2">
                        <v-flex xs12 md5 pl-2 pr-2>
                          <draggable
                            v-model="audit_data_table[detail.data_table_id]"
                            handle=".handle"
                          >
                            <transition-group>
                              <div
                                v-for="(element, index1) in audit_data_table[
                                  detail.data_table_id
                                ]"
                                :key="index1"
                              >
                                <v-layout row wrap>
                                  <v-flex xs12 md1>
                                    <i class="material-icons handle"
                                      >drag_handle</i
                                    >
                                  </v-flex>
                                  <v-flex xs12 md3 pr-2>
                                    <v-text-field
                                      v-model="element.billed"
                                      label="Billed"
                                      @focus="setCurrentVal(element)"
                                      v-on:change="
                                        resetCommentsActions(
                                          'billed',
                                          $event,
                                          index
                                        )
                                      "
                                    ></v-text-field>
                                  </v-flex>
                                  <v-flex xs12 md1 pr-2>
                                    <i
                                      class="material-icons editor-icons"
                                      @click="copyCode(element.billed)"
                                      >file_copy</i
                                    >
                                  </v-flex>
                                  <v-flex xs12 md3 pl-2>
                                    <v-text-field
                                      v-bind:id="
                                        'billedCode-' +
                                        detail.data_table_id +
                                        '-' +
                                        index1
                                      "
                                      v-model="element.audited"
                                      label="Audited"
                                      @focus="setCurrentVal(element)"
                                      v-on:change="
                                        resetCommentsActions(
                                          'audited',
                                          $event,
                                          index
                                        )
                                      "
                                    ></v-text-field>
                                  </v-flex>
                                  <v-flex xs12 md1 pr-2>
                                    <i
                                      class="material-icons editor-icons"
                                      @click="
                                        element.billed ===
                                          pasteCode(
                                            detail.data_table_id,
                                            index1
                                          )
                                      "
                                      >content_paste</i
                                    >
                                  </v-flex>

                                  <v-flex xs12 md3 class="pr-2">
                                    <v-select
                                      v-model="element.comment"
                                      :items="scoringList"
                                      item-text="comment_code"
                                      item-value="comment_code"
                                      label="Score"
                                      @change="
                                        updateCommentsIcd10(
                                          index,
                                          element,
                                          element.comment
                                        )
                                      "
                                    ></v-select>
                                  </v-flex>
                                </v-layout>
                              </div>
                            </transition-group>
                          </draggable>
                          <v-btn
                            small
                            color="success"
                            class="btn btn-secondary"
                            @click="addCode(detail.data_table_id)"
                            >Add ICD-10</v-btn
                          >
                        </v-flex>
                        <v-flex xs12 md7>
                          <v-layout row wrap>
                            <v-flex xs12 md12 class="pr-2">
                              <v-textarea
                                v-model="detail.comments"
                                box
                                label="Comments"
                                auto-grow
                                rows="3"
                                style="padding-top: 15px"
                              ></v-textarea>
                            </v-flex>
                            <v-flex xs12 md12 class="pr-2">
                              <v-textarea
                                v-model="detail.fail_action"
                                box
                                label="Fail Actions"
                                auto-grow
                                rows="3"
                                style="padding-top: 0px"
                              ></v-textarea>
                            </v-flex>
                          </v-layout>
                        </v-flex>
                      </v-layout>
                    </v-container>
                  </div>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs2>
                  <v-text-field
                    v-model="audit.audit_score"
                    label="Score"
                    disabled
                  ></v-text-field>
                </v-flex>
                <v-flex xs10>
                  <v-text-field
                    v-model="audit.fail_reason"
                    label="Failed Reason(s)"
                    disabled
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12>
                  Please upload supporting documentation
                  <FileDragDrop
                    ref="fileDragDrop"
                    @fileList="updateFileList"
                    @fileClicked="previewFile"
                  />
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12>
                  <v-textarea
                    v-model="audit.internal_comments"
                    label="Comments (Internal Use Only)"
                    auto-grow
                    box
                  ></v-textarea>
                </v-flex>
              </v-layout>
            </v-container>
            <v-btn @click="saveAudit(0)">Save</v-btn>
            <v-btn @click="saveAudit(1)">Complete</v-btn>
            <v-btn @click="cancelAudit()">Cancel</v-btn>
          </v-form>
        </v-card-text>
      </v-card>
    </v-container>
    <confirmation-dialog
      ref="confirmationDialog"
      title="Confirm"
      message="Are you sure you want to cancel your changes?"
    ></confirmation-dialog>
  </div>
</template>

<script>
import Config from "../config";
import ConfirmationDialog from "@/components/ConfirmationDialog";
import CustomSnackbar from "@/components/Snackbar";
import httpService from "../httpService";
import draggable from "vuedraggable";
import FileDragDrop from "@/components/FileDragDrop";
import { Validator } from "vee-validate";

// audit_date: new Date().toJSON()
export default {
  name: "Main",
  components: {
    ConfirmationDialog,
    CustomSnackbar,
    draggable,
    FileDragDrop,
  },
  data: () => ({
    menuDatePicker: null,
    menuPtDOB: null,
    menuPtDOService: null,
    fileViewerDialog: false,
    audit: {
      audit_date: "",
    },
    audit_data_table: [], // for draggable, doesn't like the nested loops/arrays
    copied_code: null, // for copy/paste of icd-10 codes
    backdoor: 0, // hack for recalculating the score on icd10 arrays
    newRecord: false, // used for disabling fields
    currentField: { billed: "", audited: "", field: "" }, // keep the original value of the focused field.  needed to update comment/fail reason
  }),
  beforeMount() {
    // work on this one record
    //this.audit = this.audits.find(elem => elem.audit_id == this.$route.params.audit_id);
    if (this.$route.params.audit_id != -1) {
      // normally added record using patient selection
      this.init();
    } else {
      // manually adding the charge
      // init blank records
      this.initManual();
    }
  },
  created() {
    this.$validator.extend("checkUnitsBilled", {
      getMessage(field, val) {
        return field + " is required when CPT Billed is entered";
      },
      validate(value, field) {
        console.log("validating...");
        return false;
      },
    });
  },
  computed: {
    // newRecord() {
    //   return false;
    // },
    currentUser() {
      return this.$store.getters.CURRENT_USER;
    },
    auditors() {
      return this.$store.getters.ALL_USERS;
    },
    physicians() {
      return this.$store.getters.PHYSICIAN;
    },
    scoringList() {
      return this.$store.getters.SCORING_LISTS;
    },
    auditTypes() {
      return this.$store.getters.APPLICATION_LISTS.filter(
        (row) => row.keyword === "AUDIT_TYPE"
      ).map((row) => row.value);
    },
    commentTypes() {
      return this.$store.getters.APPLICATION_LISTS.filter(
        (row) => row.keyword === "DIAGNOSIS_COMMENTS"
      ).sort((a, b) => (a.value > b.value ? 1 : b.value > a.value ? -1 : 0));
      //.map(row => row.value);
    },
    currentPhysicianName() {
      if (this.audit.npi) {
        const indx = this.physicians.findIndex(
          (obj) => obj.npi === this.audit.npi
        );
        return (
          this.physicians[indx].last_name +
          ", " +
          this.physicians[indx].first_name +
          " " +
          this.physicians[indx].title
        );
      }
    },
    auditorName() {
      if (this.audit.auditor_id >= 0) {
        const indx = this.auditors.findIndex(
          (obj) => obj.id === this.audit.auditor_id
        );
        return this.auditors[indx].fullname;
      }
    },
    isCompleted() {
      return this.audit.audit_completion;
    },
    clonedItems: function () {
      // const x = JSON.stringify(this.audit_data_table);
      return this.audit_data_table;
      // return JSON.parse(JSON.stringify(this.audit_data_table));
    },
  },
  watch: {},
  methods: {
    // WADE
    setCurrentVal(event) {
      // hold original value of field
      console.log("setCurrentVal - ", event.billed + "-" + event.audited);
      this.currentField.billed = event.billed;
      this.currentField.audited = event.audited;
      this.currentField.field = event.billed + "-" + event.audited;
    },
    resetCommentsActions(type, event, indx) {
      // type - "billed" or "audited" field being updated
      // event - new value
      // indx - index of the details section
      let updatedField;
      if (type == "billed" && this.currentField.billed != event) {
        updatedField = event + "-" + this.currentField.audited;
      } else if (type == "audited" && this.currentField.audited != event) {
        updatedField = this.currentField.billed + "-" + event;
      }

      if (
        this.audit.details[indx].comments &&
        this.currentField.field.trim() != "-"
      ) {
        this.audit.details[indx].comments = this.audit.details[
          indx
        ].comments.replace(this.currentField.field, updatedField);
      }
      if (
        this.audit.details[indx].fail_action &&
        this.currentField.field.trim() != "-"
      ) {
        this.audit.details[indx].fail_action = this.audit.details[
          indx
        ].fail_action.replace(this.currentField.field, updatedField);
      }
    },
    arrayScores() {
      // assign fail codes to array
      let arrayScores = [];
      for (let k = 0; k < this.audit_data_table.length; k++) {
        arrayScores.push(this.audit.details[k].cpt_audited_score);
        arrayScores.push(this.audit.details[k].modifier_audited_score);
        arrayScores.push(this.audit.details[k].units_audited_score);
        arrayScores.push(this.audit.details[k].service_provider_score);
        arrayScores.push(this.audit.details[k].pos_description_score);
        arrayScores.push(this.audit.details[k].pos_type_score);
        for (let i = 0; i < this.audit_data_table[k].length; i++) {
          arrayScores.push(this.audit_data_table[k][i].comment);
        }
      }
      return arrayScores;
    },
    async init() {
      httpService.get(
        `${Config.apiRoot}/audit/${this.$route.params.audit_id}`,
        (status, data) => {
          this.audit = data[0];

          // serialize icd10 fields after load
          if (this.audit.details) {
            for (let i = 0; i < this.audit.details.length; i++) {
              // console.log("this.audit.details[i] - ", this.audit.details[i]);
              if (this.audit.details[i].comments === "") {
                this.audit.details[i].comments = "{}";
              }
              this.audit.details[i].comments = JSON.stringify(
                JSON.parse(this.audit.details[i].comments),
                undefined,
                4
              ); // re-formatting for display
              if (this.audit.details[i].fail_action === "") {
                this.audit.details[i].fail_action = "{}";
              }
              this.audit.details[i].fail_action = JSON.stringify(
                JSON.parse(this.audit.details[i].fail_action),
                undefined,
                4
              ); // formatting for display
              this.audit.details[i].data_table_id = i;
              this.audit_data_table[i] = this.serializeFields(
                this.audit.details[i]
              );
              if (this.audit.details[i].comments === "null") {
                this.audit.details[i].comments = "";
              }
              if (this.audit.details[i].fail_action === "null") {
                this.audit.details[i].fail_action = "";
              }
            }
          }
          if (this.audit.audit_date === null) {
            // default current date
            const today = new Date();
            this.audit.audit_date = `${today.getFullYear()}-${
              today.getMonth() + 1
            }-${today.getDate()}`;
          }
          this.$refs.fileDragDrop.files = this.audit.files;
        }
      );
    },
    initManual() {
      // TODO - set auditor ID
      this.newRecord = true;
      this.audit = {
        id: "NEW",
        audit_date: null,
        npi: this.$route.params.npi,
        audit_provider_id: this.$route.params.provider_audit_id,
        patient_name: "",
        date_of_birth: null,
        mrn: "",
        encounter: "",
        service_date: null,
        facility: "",
        payor: "",
        audit_score: 0,
        auditor_id: this.$route.params.auditor_id,
        audit_completion_date: null,
        audit_completion: 0,
        internal_comments: "",
        details: [],
        files: [],
      };
      this.audit_data_table = [];
    },
    serializeFields(row) {
      // take individual fields (icd10_1 thru icd10_12) and convert it to an array
      // in - 1 detail row
      // returns JSON object
      let obj = [];

      for (let i = 1; i <= 12; i++) {
        const field1 = "icd10_" + i + "_billed";
        const field2 = "icd10_" + i + "_audited";
        const field3 = "icd10_" + i + "_score";
        if (row[field1] || row[field2]) {
          obj.push({
            billed: row[field1],
            audited: row[field2],
            comment: row[field3],
          });
        }
      }

      return obj;
    },
    deserializeFields(id, row) {
      // take array and convert it back to fields (icd10_1 thru icd10_12)
      // in - id of the local array element to match
      // in - array
      // modifies this.audit
      for (let i = 0; i < 12; i++) {
        const field1 = "icd10_" + (i + 1) + "_billed";
        const field2 = "icd10_" + (i + 1) + "_audited";
        const field3 = "icd10_" + (i + 1) + "_score";
        if (row) {
          if (row[i]) {
            this.audit.details[id][field1] = row[i].billed;
            this.audit.details[id][field2] = row[i].audited;
            this.audit.details[id][field3] = row[i].comment;
          } else {
            // clear entries
            this.audit.details[id][field1] = null;
            this.audit.details[id][field2] = null;
            this.audit.details[id][field3] = null;
          }
        }
      }
      return true;
    },
    addCode(id) {
      if (this.audit_data_table[id].length < 12) {
        // up to 12 entries
        this.audit_data_table[id].push({ billed: "", audited: "" });
        this.$forceUpdate();
      }
    },
    addDetailSection(id) {
      // add an ad hoc details section - key is new_row
      this.audit_data_table.push([
        {
          audited: "",
          billed: "",
          comment: null,
        },
      ]);
      // the index of new audit_data_table entry is set on the audit.details.data_table_id field
      this.audit.details.push({
        new_row: true,
        id: null,
        audit_id: this.audit.id,
        cpt_billed: null,
        cpt_audited: null,
        modifier_billed: null,
        modifier_audited: null,
        units_billed: null,
        units_audited: null,
        pass: true,
        fail_reason: "",
        comments: null,
        fail_reason_other: "",
        icd10_1_billed: null,
        icd10_1_audited: null,
        icd10_2_billed: null,
        icd10_2_audited: null,
        icd10_3_billed: null,
        icd10_3_audited: null,
        icd10_4_billed: null,
        icd10_4_audited: null,
        icd10_5_billed: null,
        icd10_5_audited: null,
        icd10_6_billed: null,
        icd10_6_audited: null,
        icd10_7_billed: null,
        icd10_7_audited: null,
        icd10_8_billed: null,
        icd10_8_audited: null,
        icd10_9_billed: null,
        icd10_9_audited: null,
        icd10_10_billed: null,
        icd10_10_audited: null,
        icd10_11_billed: null,
        icd10_11_audited: null,
        icd10_12_billed: null,
        icd10_12_audited: null,
        standard_comments_id_bad: null,
        "standard_comments_id.notused": null,
        icd10_1_comment: null,
        icd10_2_comment: null,
        icd10_3_comment: null,
        icd10_4_comment: null,
        icd10_5_comment: null,
        icd10_6_comment: null,
        icd10_7_comment: null,
        icd10_8_comment: null,
        icd10_9_comment: null,
        icd10_10_comment: null,
        icd10_11_comment: null,
        icd10_12_comment: null,
        fail_action: null,
        data_table_id: this.audit_data_table.length - 1,
      });
    },
    copyCode(value) {
      this.copied_code = value;
    },
    pasteCode(group, item) {
      if (this.copied_code) {
        // const field = "billedCode-" + group + "-" + item;
        // document.getElementById(field).value = this.copied_code;

        this.audit_data_table[group][item].audited = this.copied_code;
        this.copied_code = null;
        this.$forceUpdate();
      }
    },
    saveAudit: function (complete) {
      // if completed button is clicked, set the values otherwise
      // we don't want to change the values if just save is clicked
      if (complete) {
        const today = new Date();

        this.audit.audit_completion = 1;
        this.audit.audit_completion_date = `${today.getFullYear()}-${
          today.getMonth() + 1
        }-${today.getDate()}`;
      }
      if (this.audit.audit_completion === null) {
        this.audit.audit_completion = 0;
      }
      if (this.audit.audit_complete_date === null) {
        this.audit.audit_completion_date = "";
      }
      if (this.audit.audit_score === null) {
        this.audit.audit_score = 0;
      }

      this.$validator.validateAll().then(async (result) => {
        if (!result) {
          return;
        } else {
          // custom validation on detail records
          for (let i = 0; i < this.audit.details.length; i++) {
            // if cpt code audited is filled, units audited needs to be filled
            if (
              this.audit.details[i].cpt_audited &&
              !this.audit.details[i].units_audited
            ) {
              console.log("units audited needs to be filled");
              this.errors.add({
                field: `Units Audited-${i}`,
                msg:
                  "Units Audited is required when CPT Coded Audited is filled",
              });
              return;
            }
          }
          // deserialize icd10 fields before save
          if (this.audit.details) {
            for (let i = 0; i < this.audit.details.length; i++) {
              this.deserializeFields(
                this.audit.details[i].data_table_id,
                this.audit_data_table[this.audit.details[i].data_table_id]
              );
            }
          }
          let formData = new FormData();
          for (let key in this.audit) {
            if (key != "files" && key != "details") {
              formData.set(key, this.audit[key]);
            }
            if (key === "details") {
              formData.set(key, JSON.stringify(this.audit[key]));
            }
          }
          // deleted blobs
          let tmpDel = [];
          for (var i = 0; i < this.audit.files.length; i++) {
            if (
              this.audit.files[i].deleted &&
              this.audit.files[i].deleted == 1
            ) {
              tmpDel.push(this.audit.files[i].id);
            }
          }
          formData.set("deletedBlobs", JSON.stringify(tmpDel));

          for (var i = 0; i < this.audit.files.length; i++) {
            formData.append("files[" + i + "]", this.audit.files[i]);
          }
          httpService.put(
            `${Config.apiRoot}/audit/${this.audit.id}`,
            formData,
            (status, data) => {
              if (data.success) {
                this.showMessage("success", "Record sucessfully saved");
                if (data.assignedId) {
                  // from manual charge, update the audit id
                  this.audit.id = data.assignedId;
                }
                this.init(); // force refresh of the page to get the new ids
              } else {
                this.showMessage(
                  "error",
                  "There was a problem saving your changes.  MSG - " +
                    data.message.originalError.info.message
                );
              }
            }
          );
        }
      });
    },
    cancelAudit: function () {
      this.$refs.confirmationDialog.confirmation().then((res) => {
        if (res) {
          this.$router.go(-1);
        }
      });
    },
    updateFileList(obj) {
      // Updating parent file listing
      this.audit.files = obj;
    },
    previewFile(key) {
      this.viewBlob(this.audit.files[key].blob.data);

      // this.$refs.fileViewerDialog.show(
      //   this.audit.files[key].originalname,
      //   `data:application/pdf;base64,${Buffer.from(
      //     this.audit.files[key].blob.data
      //   ).toString("base64")}`
      // );
    },
    updateCommentsIcd10(indx, obj, score) {
      // these need to be handled differently from the regular comments
      // JSON will make it easier to maintain programatically?
      let xx = {};
      if (this.audit.details[indx].comments) {
        xx = JSON.parse(this.audit.details[indx].comments);
      }
      xx[obj.billed + "-" + obj.audited] = this.scoringList.find(
        (item) => item.comment_code == score
      ).comment;

      this.audit.details[indx].comments = JSON.stringify(xx, undefined, 4);
      this.updateFailActionsIcd10(indx, obj, score);
      this.computeScore();
      this.computeFailReason();
      // WADE      this.updateFailReasonsIcd10(indx, obj, score);
    },
    updateFailActionsIcd10(indx, obj, score) {
      // these need to be handled differently from the regular comments
      // JSON will make it easier to maintain programatically?

      // WADE - fix override4 and 5  need the higher object

      // (ICD-10 Code Audited)
      const override1 = ["D01", "D02", "D07", "D08"];
      // ____
      const override2 = ["D03", "D06"];
      // (CPT Code Audited)
      const override3 = [
        "M10",
        "B05",
        "B04",
        "C01",
        "E01",
        "E02",
        "E03",
        "E04",
        "E05",
        "E06",
        "E09",
        "E10",
        "E12",
        "E13",
        "E14",
        "E16",
        "E17",
        "E18",
        "E19",
        "E20",
        "E21",
        "E23",
        "E24",
        "M06",
        "P03",
        "P04",
        "E15",
        "D01",
        "D02",
        "D07",
        "D08",
      ];

      let xx = {};
      if (this.audit.details[indx].fail_action) {
        xx = JSON.parse(this.audit.details[indx].fail_action);
      }
      let tmpReason = this.scoringList.find(
        (item) => item.comment_code == score
      ).fail_action;
      if (override1.includes(score)) {
        // tmpReason = replace with obj.audited
        tmpReason = tmpReason.replace("(ICD-10 Code Audited)", obj.audited);
      }
      if (override2.includes(score)) {
        // prettier-ignore
        tmpReason = tmpReason.replace(/\_+/g, obj.billed);
      }
      if (override3.includes(score)) {
        tmpReason = tmpReason.replace("(CPT Code Audited)", obj.audited);
      }

      xx[obj.billed + "-" + obj.audited] = tmpReason;
      // xx[obj.billed + "-" + obj.audited] = this.scoringList.find(
      //   item => item.comment_code == score
      // ).fail_action;

      this.audit.details[indx].fail_action = JSON.stringify(xx, undefined, 4);
    },
    updateComments(indx, field, score) {
      // JSON will make it easier to maintain programatically?
      let xx = {};
      if (
        this.audit.details[indx].comments &&
        this.audit.details[indx].comments != "null"
      ) {
        xx = JSON.parse(this.audit.details[indx].comments);
      }
      xx[field] = this.scoringList.find(
        (item) => item.comment_code == score
      ).comment;

      this.audit.details[indx].comments = JSON.stringify(xx, undefined, 4);
      this.updateFailActions(indx, field, score);
      this.computeScore();
      this.computeFailReason();
    },
    updateFailActions(indx, field, score) {
      // JSON will make it easier to maintain programatically?

      // (Modifier Audited)
      const override1 = ["B26"];
      // (Units Audited)
      const override2 = ["B03"];
      // (Procedure Code Audited)
      const override3 = ["P03", "P04"];

      let xx = {};
      if (
        this.audit.details[indx].fail_action &&
        this.audit.details[indx].fail_action != "null"
      ) {
        xx = JSON.parse(this.audit.details[indx].fail_action);
      }
      xx[field] = this.scoringList.find(
        (item) => item.comment_code == score
      ).fail_action;

      if (override1.includes(score)) {
        xx[field] = xx[field].replace(
          "(Modifier Audited)",
          this.audit.details[indx].modifier_audited
        );
      }
      if (override2.includes(score)) {
        xx[field] = xx[field].replace(
          "(Units Audited)",
          this.audit.details[indx].units_audited
        );
      }
      if (override3.includes(score)) {
        xx[field] = xx[field].replace(
          "(CPT Code Audited)",
          this.audit.details[indx].cpt_audited
        );
      }
      this.audit.details[indx].fail_action = JSON.stringify(xx, undefined, 4);
    },
    showMessage: function (type, message) {
      this.$refs.customSnackbar.showSnackbar(message, type, 3000, true);
    },
    computeFailReason() {
      // list unique failed_reasons for all scores
      const arrayScores = this.arrayScores();

      let reasons = [];
      for (let i = 0; i < arrayScores.length; i++) {
        let tmpScore = this.scoringList.find(
          (item) => item.comment_code == arrayScores[i]
        );
        if (
          tmpScore &&
          tmpScore.fail_reason &&
          reasons.indexOf(tmpScore.fail_reason) === -1
        ) {
          reasons.push(tmpScore.fail_reason); // no dups
        }
      }
      this.audit.fail_reason = reasons.toString();
    },
    computeScore() {
      // take all the scores for the record.
      // find the highest offender (hierarchy) and use that points deducted
      let arrayScores = [];
      for (let k = 0; k < this.audit.details.length; k++) {
        // WADE - changed from audit_data_table 5.18
        arrayScores.push(this.audit.details[k].cpt_billed_score);
        arrayScores.push(this.audit.details[k].modifier_billed_score);
        arrayScores.push(this.audit.details[k].units_billed_score);
        arrayScores.push(this.audit.details[k].cpt_audited_score);
        arrayScores.push(this.audit.details[k].modifier_audited_score);
        arrayScores.push(this.audit.details[k].units_audited_score);
        arrayScores.push(this.audit.details[k].service_provider_score);
        arrayScores.push(this.audit.details[k].pos_description_score);
        arrayScores.push(this.audit.details[k].pos_type_score);
        for (let i = 0; i < this.audit_data_table[k].length; i++) {
          arrayScores.push(this.audit_data_table[k][i].comment);
        }
      }

      let currentScore = {};
      currentScore.hierarchy = 999;
      currentScore.pointsDeducted = 0;
      for (let i = 0; i < arrayScores.length; i++) {
        let tmpScore = this.scoringList.find(
          (item) => item.comment_code == arrayScores[i]
        );
        if (
          tmpScore &&
          tmpScore.hierarchy &&
          currentScore.hierarchy > tmpScore.hierarchy
        ) {
          currentScore.hierarchy = tmpScore.hierarchy;
          currentScore.pointsDeducted = tmpScore.points;
        }
      }
      this.audit.audit_score = currentScore.pointsDeducted;
    },
  },
};
</script>

