<template>
  <div>
    <custom-snackbar ref="customSnackbar"></custom-snackbar>
    <v-card v-if="selectedPhysicianAuditId" class="mb-2">
      <v-card-title
        class="headline font-weight-regular chsli-color white--text"
      >Add Encounter - Manual</v-card-title>
      <v-card-text>
        <v-form ref="form">
          <v-container fluid>
            <v-subheader>Encounter Details</v-subheader>
            <v-divider></v-divider>
            <v-layout row wrap>
              <v-flex xs12 md5 class="pr-1">
                <v-menu
                  ref="menuDatePicker"
                  :close-on-content-click="false"
                  v-model="menuDatePicker"
                  :nudge-right="40"
                  :return-value.sync="audits[0].audit_date"
                  lazy
                  transition="scale-transition"
                  offset-y
                  full-width
                  min-width="290px"
                >
                  <v-text-field
                    slot="activator"
                    v-model="audits[0].audit_date"
                    label="Audit Date"
                    prepend-icon="event"
                    readonly
                    data-vv-name="Audit Date"
                    :error-messages="errors.collect('Audit Date')"
                  ></v-text-field>
                  <!-- v-validate = "{rules: { required: true }}" -->
                  <v-date-picker
                    v-model="audits[0].audit_date"
                    @input="$refs.menuDatePicker.save(audits[0].audit_date)"
                  ></v-date-picker>
                </v-menu>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs12 md4 class="pr-1">
                <v-text-field :value="currentPhysician" label="Provider Name"></v-text-field>
              </v-flex>
              <v-flex xs12 md3 class="pr-1">
                <v-text-field v-model="selectedNpi" label="Provider NPI"></v-text-field>
              </v-flex>
              <v-flex xs12 md5 class="pr-1">
                <v-text-field v-model="audits[0].facility" label="Facility"></v-text-field>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs12 md5 class="pr-1">
                <v-text-field v-model="audits[0].patient_name" label="Patient Name"></v-text-field>
              </v-flex>
              <v-flex xs12 md3 class="pr-1">
                <v-menu
                  ref="menuDateDOBPicker"
                  :close-on-content-click="false"
                  v-model="menuDateDOBPicker"
                  :nudge-right="40"
                  :return-value.sync="audits[0].date_of_birth"
                  lazy
                  transition="scale-transition"
                  offset-y
                  full-width
                  min-width="290px"
                >
                  <v-text-field
                    slot="activator"
                    v-model="audits[0].date_of_birth"
                    label="Date of Birth"
                    prepend-icon="event"
                    readonly
                  ></v-text-field>
                  <v-date-picker
                    v-model="audits[0].date_of_birth"
                    @input="$refs.menuDateDOBPicker.save(audits[0].date_of_birth)"
                  ></v-date-picker>
                </v-menu>
              </v-flex>
              <v-flex xs12 md2 class="pr-1">
                <v-text-field v-model="audits[0].payor" label="Payer"></v-text-field>
              </v-flex>
              <v-flex xs12 md2 class="pr-1">
                <v-menu
                  ref="menuDateServicePicker"
                  :close-on-content-click="false"
                  v-model="menuDateServicePicker"
                  :nudge-right="40"
                  :return-value.sync="audits[0].service_date"
                  lazy
                  transition="scale-transition"
                  offset-y
                  full-width
                  min-width="290px"
                >
                  <v-text-field
                    slot="activator"
                    v-model="audits[0].service_date"
                    label="Service Date"
                    prepend-icon="event"
                    readonly
                  ></v-text-field>
                  <v-date-picker
                    v-model="audits[0].service_date"
                    @input="$refs.menuDateServicePicker.save(audits[0].service_date)"
                  ></v-date-picker>
                </v-menu>
              </v-flex>
            </v-layout>
            <v-subheader>Coding Details</v-subheader>
            <v-divider></v-divider>
            <v-layout row wrap>
              <v-flex xs12>
                <div
                  v-for="(detail, index) in audits[0].details"
                  :key="detail.cpt_billed"
                  class="grey lighten-5"
                >
                  <v-container mt-2>
                    <v-layout row wrap>
                      <v-flex xs12 md4 class="pr-1">
                        <v-layout row wrap>
                          <v-flex xs12>
                            <v-text-field v-model="detail.cpt_billed" label="CPT Code Billed"></v-text-field>
                          </v-flex>
                        </v-layout>
                        <v-layout row wrap>
                          <v-flex xs12>
                            <v-text-field v-model="detail.cpt_audited" label="CPT Code Audited"></v-text-field>
                          </v-flex>
                        </v-layout>
                      </v-flex>
                      <v-flex xs12 md4 class="pr-1">
                        <v-layout row wrap>
                          <v-flex xs12>
                            <v-text-field v-model="detail.modifier_billed" label="Modifier Billed"></v-text-field>
                          </v-flex>
                        </v-layout>
                        <v-layout row wrap>
                          <v-flex xs12>
                            <v-text-field
                              v-model="detail.modifier_audited"
                              label="Modifier Audited"
                            ></v-text-field>
                          </v-flex>
                        </v-layout>
                      </v-flex>
                      <v-flex xs12 md4 class="pr-1">
                        <v-layout row wrap>
                          <v-flex xs12>
                            <v-text-field v-model="detail.units_billed" label="Units Billed"></v-text-field>
                          </v-flex>
                        </v-layout>
                        <v-layout row wrap>
                          <v-flex xs12>
                            <v-text-field
                              v-model="detail.units_audited"
                              label="Units Audited"
                              :name="'Units Audited-' + index"
                              v-validate="'checkUnitsBilled'"
                              :error-messages="errors.collect(`Units Audited-${index}`)"
                            ></v-text-field>
                          </v-flex>
                        </v-layout>
                      </v-flex>
                    </v-layout>
                    <v-layout row wrap class="mt-2">
                      <v-flex xs12 md5 pl-2 pr-2>
                        <!-- WADE -->
                        <draggable
                          v-model="audit_data_table[detail.data_table_id]"
                          handle=".handle"
                        >
                          <transition-group>
                            <div
                              v-for="(element, index) in audit_data_table[detail.data_table_id]"
                              :key="element.billed"
                            >
                              <v-layout row wrap>
                                <v-flex xs12 md1>
                                  <i class="material-icons handle">drag_handle</i>
                                </v-flex>
                                <v-flex xs12 md3 pr-2>
                                  <v-text-field v-model="element.billed" label="Billed"></v-text-field>
                                </v-flex>
                                <v-flex xs12 md1 pr-2>
                                  <i
                                    class="material-icons editor-icons"
                                    @click="copyCode(element.billed)"
                                  >file_copy</i>
                                </v-flex>
                                <v-flex xs12 md3 pl-2>
                                  <v-text-field
                                    v-bind:id="'billedCode-' + detail.data_table_id + '-' + index"
                                    v-model="element.audited"
                                    label="Audited"
                                  ></v-text-field>
                                </v-flex>
                                <v-flex xs12 md1 pr-2>
                                  <i
                                    class="material-icons editor-icons"
                                    @click="element.billed === pasteCode(detail.data_table_id, index)"
                                  >content_paste</i>
                                </v-flex>
                                <v-flex xs12 md3 class="pr-2">
                                  <v-select
                                    v-model="element.comment"
                                    :items="commentTypes"
                                    item-text="value"
                                    item-value="value"
                                    label="Comment"
                                  ></v-select>
                                </v-flex>
                              </v-layout>
                            </div>
                          </transition-group>
                        </draggable>
                        <!-- WADE -->
                        <v-btn
                          small
                          color="success"
                          class="btn btn-secondary"
                          @click="addCode(detail.data_table_id)"
                        >Add ICD-10</v-btn>
                      </v-flex>
                      <!--                      
                        <v-flex xs12 md2 pl-2 pr-2>
                          <v-text-field v-model="element.audited" label="Audited" class="audited"></v-text-field>
                        </v-flex>
                      -->
                      <v-flex xs12 md7>
                        Pass/Fail
                        <v-layout>
                          <v-flex xs12 class1="pr-1 pl-1">
                            <v-container>
                              <v-layout row wrap>
                                <v-flex xs12 md7>
                                  <v-radio-group
                                    v-model="detail.pass"
                                    row
                                    @change="testPassFail(detail)"
                                  >
                                    <v-radio label="Pass - No Action" v-bind:value="true"></v-radio>
                                    <v-radio label="Fail - Action Required" v-bind:value="false"></v-radio>
                                  </v-radio-group>
                                </v-flex>
                                <v-flex xs12 md5 pr-1>
                                  <v-container>
                                    <v-layout row wrap>
                                      <v-flex xs12>
                                        <v-select
                                          v-model="detail.fail_reason"
                                          :items="failReasons"
                                          label="Fail Reason"
                                          v-if="detail.pass === false"
                                          @change="testFailReason(detail)"
                                        ></v-select>
                                      </v-flex>
                                    </v-layout>
                                    <v-layout row wrap>
                                      <v-flex xs12>
                                        <v-text-field
                                          v-model="detail.fail_reason_other"
                                          label="Other Reason"
                                          v-if="detail.fail_reason === 'Other'"
                                        ></v-text-field>
                                      </v-flex>
                                    </v-layout>
                                    <v-layout row wrap>
                                      <v-flex xs12>
                                        <v-combobox
                                          v-model="detail.fail_action"
                                          :items="failActions"
                                          label="Fail Action"
                                          v-if="detail.pass === false"
                                        ></v-combobox>
                                      </v-flex>
                                    </v-layout>
                                  </v-container>
                                </v-flex>
                              </v-layout>
                            </v-container>
                          </v-flex>
                        </v-layout>
                        <v-layout row wrap>
                          <!--
                            <v-flex xs12 md2 class="pr-2">
                              <v-select
                                v-model="detail.standard_comments_id"
                                :items="commentTypes"
                                item-text="value"
                                item-value="id"
                                label="Standard Comments"
                              ></v-select>
                            </v-flex>
                          -->
                          <v-flex xs12 md12 class="pr-2">
                            <v-textarea
                              v-model="detail.comments"
                              box
                              label="Comments"
                              auto-grow
                              rows="3"
                              style="padding-top:15px;"
                            ></v-textarea>
                          </v-flex>
                        </v-layout>
                      </v-flex>
                    </v-layout>
                  </v-container>
                </div>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-btn
                small
                color="success"
                class="btn btn-secondary"
                @click="addCTPCode()"
              >Add CPT Code</v-btn>
            </v-layout>
          </v-container>
          <v-btn @click="saveAudit()">Save</v-btn>
          <v-btn @click="cancelAudit()">Cancel</v-btn>
        </v-form>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import Config from "../config";
import CustomSnackbar from "@/components/Snackbar";
import draggable from "vuedraggable";
import httpService from "../httpService";

export default {
  name: "Main",
  components: { CustomSnackbar, draggable },
  data: () => ({
    selectedPractice: null,
    selectedPhysicianAuditId: null,
    menuDatePicker: null,
    menuDateDOBPicker: null,
    menuDateServicePicker: null,
    selectedNpi: "",
    audits: [{ audit_date: null, details: [] }],
    fileViewerDialog: false,
    audit_data_table: [], // for draggable, doesn't like the nested loops/arrays
    copied_code: null // for copy/paste of icd-10 codes
  }),
  created() {},
  computed: {
    currentUser() {
      return this.$store.getters.CURRENT_USER;
    },
    physicians() {
      return this.$store.getters.PHYSICIAN;
    },
    currentPhysician() {
      const tmpPhysician = this.physicians.find(
        el => el.npi == this.selectedNpi
      );
      return tmpPhysician.last_name + ", " + tmpPhysician.first_name;
    },
    commentTypes() {
      return this.$store.getters.APPLICATION_LISTS.filter(
        row => row.keyword === "DIAGNOSIS_COMMENTS"
      ).sort((a, b) => (a.value > b.value ? 1 : b.value > a.value ? -1 : 0));
      //.map(row => row.value);
    }
  },
  methods: {
    showComponent(id, npi) {
      this.selectedPhysicianAuditId = true;
      this.selectedNpi = npi;

      this.audits[0] = this.initDetailRow(0);

      // serialize icd10 fields after load.  should have only 1 empty row
      if (this.audits[0].details) {
        for (let i = 0; i < this.audits[0].details.length; i++) {
          this.audits[0].details[i].data_table_id = i;
          this.audit_data_table[i] = this.serializeFields(
            this.audits[0].details[i]
          );
        }
      }
    },
    showMessage: function(type, message) {
      this.$refs.customSnackbar.showSnackbar(message, type, 5000, true);
    },
    serializeFields(row) {
      // take individual fields (icd10_1 thru icd10_12) and convert it to an array
      // in - 1 detail row
      // returns JSON object
      let obj = [];
      for (let i = 1; i <= 12; i++) {
        const field1 = "icd10_" + i + "_billed";
        const field2 = "icd10_" + i + "_audited";
        const field3 = "icd10_" + i + "_comment";
        if (row[field1] || row[field1]) {
          obj.push({
            billed: row[field1],
            audited: row[field2],
            comment: row[field3]
          });
        }
      }
      return obj;
    },
    deserializeFields(id, row) {
      // take array and convert it back to fields (icd10_1 thru icd10_12)
      // in - id of the local array element to match
      // in - array
      // modifies this.audit
      for (let i = 0; i < 12; i++) {
        const field1 = "icd10_" + (i + 1) + "_billed";
        const field2 = "icd10_" + (i + 1) + "_audited";
        const field3 = "icd10_" + (i + 1) + "_comment";
        if (row[i]) {
          this.audits[0].details[id][field1] = row[i].billed;
          this.audits[0].details[id][field2] = row[i].audited;
          this.audits[0].details[id][field3] = row[i].comment;
        } else {
          // clear entries
          this.audits[0].details[id][field1] = null;
          this.audits[0].details[id][field2] = null;
          this.audits[0].details[id][field3] = null;
        }
      }
      return true;
    },
    addCode(id) {
      if (this.audit_data_table[id].length < 12) {
        // up to 12 entries
        this.audit_data_table[id].push({ billed: "", audited: "" });
        this.$forceUpdate();
      }
    },
    copyCode(value) {
      this.copied_code = value;
    },
    pasteCode(group, item) {
      if (this.copied_code) {
        // const field = "billedCode-" + group + "-" + item;
        // document.getElementById(field).value = this.copied_code;

        this.audit_data_table[group][item].audited = this.copied_code;
        this.copied_code = null;
        this.$forceUpdate();
      }
    },
    addCTPCode() {
      this.audits.details[this.audits.details.length].push(
        this.initDetailRow(this.audits[0].details.length)
      );
      // WADE 7.3 WADE
      // need to add it to the serialized array
      this.audit_data_table[this.audit_data_table.length].push({
        billed: "",
        audited: "",
        comment: ""
      });

      this.$forceUpdate();
    },
    initDetailRow(kntr) {
      return {
        id: null,
        audit_id: null,
        audit_date: null,
        cpt_billed: null,
        cpt_audited: null,
        modifier_billed: null,
        modifier_audited: null,
        units_billed: null,
        units_audited: null,
        pass: null,
        fail_reason: null,
        comments: null,
        fail_reason_other: null,
        icd10_1_billed: null,
        icd10_1_audited: null,
        icd10_2_billed: null,
        icd10_2_audited: null,
        icd10_3_billed: null,
        icd10_3_audited: null,
        icd10_4_billed: null,
        icd10_4_audited: null,
        icd10_5_billed: null,
        icd10_5_audited: null,
        icd10_6_billed: null,
        icd10_6_audited: null,
        icd10_7_billed: null,
        icd10_7_audited: null,
        icd10_8_billed: null,
        icd10_8_audited: null,
        icd10_9_billed: null,
        icd10_9_audited: null,
        icd10_10_billed: null,
        icd10_10_audited: null,
        icd10_11_billed: null,
        icd10_11_audited: null,
        icd10_12_billed: null,
        icd10_12_audited: null,
        standard_comments_id_bad: null,
        icd10_1_comment: null,
        icd10_2_comment: null,
        icd10_3_comment: null,
        icd10_4_comment: null,
        icd10_5_comment: null,
        icd10_6_comment: null,
        icd10_7_comment: null,
        icd10_8_comment: null,
        icd10_9_comment: null,
        icd10_10_comment: null,
        icd10_11_comment: null,
        icd10_12_comment: null,
        fail_action: null,
        data_table_id: kntr
      };
    }
  }
};
</script>

<style>
.record-selector .v-list__tile {
  height: auto;
}
</style>
