<template>
  <div>
    <custom-snackbar ref="customSnackbar"></custom-snackbar>
    <v-card v-if="selectedPhysicianAuditId" class="mb-2">
      <v-card-title class="headline font-weight-regular chsli-color white--text">Patient Selection</v-card-title>
      <v-card-text>
        <v-layout row wrap>
          <v-flex xs12 md6>
            <v-text-field disabled :value="currentPhysician"></v-text-field>
          </v-flex>
          <v-flex xs12 md3>
            <v-menu
              :close-on-content-click="false"
              v-model="menu1"
              :nudge-right="40"
              lazy
              transition="scale-transition"
              offset-y
              full-width
              max-width="290px"
              min-width="290px"
            >
              <v-text-field
                slot="activator"
                v-model="auditBeginDate"
                label="Begin Date"
                persistent-hint
                prepend-icon="event"
                readonly
              ></v-text-field>
              <v-date-picker v-model="auditBeginDate" no-title @input="menu1 = false"></v-date-picker>
            </v-menu>
          </v-flex>
          <v-flex xs12 md3>
            <v-menu
              :close-on-content-click="false"
              v-model="menu2"
              :nudge-right="40"
              lazy
              transition="scale-transition"
              offset-y
              full-width
              max-width="290px"
              min-width="290px"
            >
              <v-text-field
                slot="activator"
                v-model="auditEndDate"
                label="End Date"
                persistent-hint
                prepend-icon="event"
                readonly
              ></v-text-field>
              <v-date-picker v-model="auditEndDate" no-title @input="menu2 = false"></v-date-picker>
            </v-menu>
          </v-flex>
        </v-layout>
        <v-layout row wrap>
          <v-flex xs12 md6>
            <v-autocomplete
              v-model="auditPayer"
              :items="payerListing"
              persistent-hint
              item-text="original_payor"
              label="Payer"
              multiple
            ></v-autocomplete>
          </v-flex>
        </v-layout>
        <v-layout row wrap>
          <v-flex xs12>
            <v-btn block color="blue" @click="searchProcedures()">Search</v-btn>
          </v-flex>
        </v-layout>
        <v-progress-linear v-if="showProgress" :indeterminate="true"></v-progress-linear>
      </v-card-text>
    </v-card>

    <v-layout row wrap v-if="initialSearch">
      <v-flex xs12 md7 class="pr-1">
        <v-card class="mb-2">
          <v-card-title
            class="headline font-weight-regular chsli-color white--text"
          >Procedure Summary</v-card-title>
          <v-card-text>
            <v-list>
              <v-list-tile
                v-if="this.distinctProcedures.length === 0 && initialSearch"
              >No Data Found</v-list-tile>
              <v-list-group v-for="procedure in this.distinctProcedures" :key="procedure" no-action>
                <v-list-tile slot="activator">
                  <v-list-tile-content>
                    <v-list-tile-title>{{ procedure }} ({{ listGroupCountForProcedures(procedure) }})</v-list-tile-title>
                  </v-list-tile-content>
                </v-list-tile>
                <v-list-tile
                  class="record-selector"
                  v-for="(item, index) in procedureListing"
                  :key="index"
                  v-if="item.cpt === procedure && item.visible != false"
                  @click="addPatientToList(item.encounter, item.service_date)"
                >
                  <v-list-tile-content>
                    <v-list-tile-title>{{ item.patient_name }}</v-list-tile-title>
                    <v-list-tile-sub-title>
                      <v-layout row wrap>
                        <v-flex
                          xs12
                          lg6
                        >CPT: {{ patientCptList(item.encounter, item.service_date).toString().replace(/[\"]/, "") }}</v-flex>
                        <v-flex xs12 lg6>Service Date: {{ item.service_date }}</v-flex>
                      </v-layout>
                      <v-layout row wrap>
                        <v-flex xs12>Location: {{ item.location }}</v-flex>
                        <v-flex xs12>Diagnosis Codes: {{ item.icd10_billed }}</v-flex>
                      </v-layout>
                    </v-list-tile-sub-title>
                  </v-list-tile-content>
                  <v-list-tile-action>
                    <v-icon color="grey darken-1">arrow_forward</v-icon>
                  </v-list-tile-action>
                </v-list-tile>
              </v-list-group>
            </v-list>
          </v-card-text>
        </v-card>
      </v-flex>
      <v-flex xs21 md5>
        <v-card class="mb-2">
          <v-card-title class="headline font-weight-regular chsli-color white--text">Selected</v-card-title>
          <v-card-text>
            <v-list>
              <v-list-tile
                v-if="selectedEncounters.length === 0 && initialSearch"
              >No Records Selected</v-list-tile>
              <v-list-tile
                class="record-selector"
                v-for="(sel, index) in selectedEncounters"
                :key="index"
                @click="popPatientFromList(sel.enc, sel.svcDate)"
              >
                <v-list-tile-action>
                  <v-icon>arrow_back</v-icon>
                </v-list-tile-action>
                <v-list-tile-content>
                  <v-list-tile-title>{{ patientFromEncounter(sel.enc).patient_name }}</v-list-tile-title>
                  <v-list-tile-sub-title>
                    <v-layout row wrap>
                      <v-flex
                        xs12
                        lg6
                      >CPT: {{ patientCptList(sel.enc, sel.svcDate).toString().replace(/[\"]/, "") }}</v-flex>
                      <v-flex xs12 lg6>Service Date: {{ sel.svcDate }}</v-flex>
                    </v-layout>
                  </v-list-tile-sub-title>
                </v-list-tile-content>
              </v-list-tile>
            </v-list>
          </v-card-text>
          <div class="text-xs-center">
            <v-btn v-if="selectedEncounters.length" @click="createAuditsPreCheck()">Create Audits</v-btn>
          </div>
        </v-card>
      </v-flex>
    </v-layout>
    <v-row justify="center">
      <v-dialog v-model="dialogConfirmEncounters" persistent max-width="290">
        <v-card>
          <v-card-title class="headline">Number of Encounters</v-card-title>
          <v-card-text>Less then 10 Encounters have been selected. Are you sure you want to Create Audit?</v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn text @click="createAudits">Yes</v-btn>
            <v-btn text @click="dialogConfirmEncounters = false">No</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>
  </div>
</template>

<script>
import Config from "../config";
import CustomSnackbar from "@/components/Snackbar";
import httpService from "../httpService";

export default {
  name: "Main",
  components: { CustomSnackbar },
  data: (vm) => ({
    audits: [],
    selectedPractice: null,
    selectedPhysicianAuditId: null,
    preSelectedEncounters: [], // have already been added to the provider audits (need to filter out)
    payerListing: [],
    procedureListing: [],
    selectedEncounters: [],
    menu1: false,
    menu2: false,
    initialSearch: false,
    auditEndDate: vm.formatDate(new Date()),
    auditBeginDate: vm.formatDate(
      new Date(new Date().setDate(new Date().getDate() - 120))
    ),
    auditPayer: null,
    selectedNpi: "",
    dialogConfirmEncounters: false,
    showProgress: false,
  }),
  mounted() {
    httpService.get(`${Config.apiRoot}/transactions/payers`, (status, data) => {
      // reorder so medicare and medicaid are first
      const reorderItems = ["MEDICARE", "MEDICAID", "ALL PAYERS"];
      reorderItems.forEach((el) => {
        let indx = data
          .map(function (e) {
            return e.original_payor;
          })
          .indexOf(el);

        if (indx > -1) {
          data.splice(indx, 1);
        }
        data.unshift(el);
      });

      this.payerListing = [...data];
    });
  },
  created() {},
  computed: {
    currentUser() {
      return this.$store.getters.CURRENT_USER;
    },
    physicians() {
      return this.$store.getters.PHYSICIAN;
    },
    currentPhysician() {
      const tmpPhysician = this.physicians.find(
        (el) => el.npi == this.selectedNpi
      );
      return tmpPhysician.last_name + ", " + tmpPhysician.first_name;
    },
    distinctProcedures() {
      return [...new Set(this.procedureListing.map((obj) => obj.cpt))];
    },
  },
  methods: {
    formatDate(date) {
      var d = new Date(date),
        month = "" + (d.getMonth() + 1),
        day = "" + d.getDate(),
        year = d.getFullYear();

      if (month.length < 2) month = "0" + month;
      if (day.length < 2) day = "0" + day;

      return [year, month, day].join("-");
    },
    showComponent(id, npi, encList = []) {
      this.selectedPhysicianAuditId = id;
      this.selectedNpi = npi;
      this.preSelectedEncounters = encList;
    },
    showMessage: function (type, message) {
      this.$refs.customSnackbar.showSnackbar(message, type, 5000, true);
    },
    async searchProcedures() {
      this.showProgress = true;
      this.procedureListing = [];
      this.selectedEncounters = [];

      const qs = `${this.selectedNpi}/${this.auditBeginDate}/${this.auditEndDate}/${this.auditPayer}`;
      httpService.get(
        `${Config.apiRoot}/transactions/${qs}`,
        (status, data) => {
          if (data) {
            // this.procedureListing = data.filter(
            //   (i) => !this.preSelectedEncounters.includes(i.encounter)
            // );
            //WADE TODO
            this.procedureListing = data.filter(
              (i) =>
                !this.preSelectedEncounters.find(
                  (preEl) =>
                    i.patient_name == preEl.ptName &&
                    i.service_date == preEl.svcDate
                )
            );
            // sample
            // const r = array.filter((elem) => !anotherArray.find(({ id }) => elem.id === id) && elem.sub);
          }
          this.initialSearch = true; // show no data only after the first search is done
          this.showProgress = false;
        }
      );
    },
    patientCptList(pt, svcDt) {
      // WADE -
      // pt - patient encounter
      // svcDt - service date
      // return array of CPT codes for a specific patient by service date
      let cptList = [
        ...new Set(
          this.patientRecords(pt).filter((obj) => obj.service_date == svcDt)
        ),
      ];
      return [...new Set(cptList.map((obj) => obj.cpt))];
    },
    patientRecords(pt) {
      // pt - patient encounter
      // return array of rows for a specific patient
      return this.procedureListing.filter((obj) => obj.encounter === pt);
    },
    listGroupCountForProcedures(proc) {
      return this.procedureListing.filter((obj) => obj.cpt === proc).length;
    },
    addPatientToList(enc, svcDate) {
      // Add encounter + service date to list to create audits
      let added = false;
      for (let i = 0; i < this.selectedEncounters.length; i++) {
        if (
          this.selectedEncounters[0].enc == enc &&
          this.selectedEncounters[0].svcDate == svcDate
        ) {
          added = true;
        }
      }

      if (!added) {
        this.selectedEncounters.push({ enc: enc, svcDate: svcDate });

        // hide patient in entire list
        this.procedureListing.map((el) => {
          if (el.encounter === enc && el.service_date === svcDate) {
            el.visible = false;
          }
        });
      }
    },
    popPatientFromList(enc, svcDate) {
      // Remove encounter from the list of audits
      this.selectedEncounters = this.selectedEncounters.filter((obj) => {
        return obj.enc != enc && obj.svcDate != svcDate;
      });

      // show patient in entire list
      this.procedureListing.map((el) => {
        if (el.encounter === enc) {
          el.visible = true;
        }
      });
    },
    async createAuditsPreCheck() {
      if (this.selectedEncounters.length < 10) {
        // diplay message
        this.dialogConfirmEncounters = true;
      } else {
        this.createAudits();
      }
    },
    async createAudits() {
      // create audit records in database
      this.dialogConfirmEncounters = false;
      httpService.post(
        `${Config.apiRoot}/transactions/createAudits`,
        {
          physicianAuditId: this.selectedPhysicianAuditId,
          physicianNpi: this.selectedNpi,
          encounters: this.selectedEncounters,
        },
        (status, data) => {
          if (!data.status) {
            this.showMessage(
              "error",
              "there was a problem creating the audit records"
            );
          }
          this.showMessage(
            "success",
            "Audit records created.  TODO - create detailed message"
          );
          this.selectedEncounters = [];

          // let parent know that we saved, if they're listening
          //$emit("created");
          this.$emit("created");
        }
      );
      //     });
    },
    patientFromEncounter(enc) {
      return this.procedureListing.find((elem) => elem.encounter === enc);
    },
  },
};
</script>

<style>
.record-selector .v-list__tile {
  height: auto;
}
</style>
