<template>
  <div>
    <v-container v-if="showPhysicianInfo">
      <custom-snackbar ref="customSnackbar"></custom-snackbar>
      <v-card>
        <v-card-title
          class="headline font-weight-regular chsli-color white--text"
          >Provider</v-card-title
        >
        <v-card-text>
          <v-form ref="form">
            <v-container fluid>
              <v-subheader>Audit Details</v-subheader>
              <v-divider></v-divider>
              <v-layout row wrap>
                <v-flex xs12 md3>
                  <v-text-field
                    v-model="audit[0].id"
                    label="Audit ID"
                    disabled
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md3>
                  <v-text-field
                    v-model="auditorName"
                    label="Auditor"
                    disabled
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md3>
                  <v-select
                    v-model="audit[0].additional_auditor_id"
                    :items="auditors"
                    item-text="fullname"
                    item-value="id"
                    multiple
                    label="Additional Auditors"
                    :disabled="!isAdministrator"
                  ></v-select>
                </v-flex>
                <v-flex xs12 md3>
                  <v-text-field
                    v-model="audit[0].audit_date"
                    label="Audit Date"
                    disabled
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md4 style="display: flex">
                  <v-text-field
                    v-model="audit[0].provider_name"
                    label="Provider Name"
                    disabled
                  ></v-text-field>
                  <a
                    v-if="audit[0].provider_email"
                    :href="`mailto:${audit[0].provider_email}`"
                    style="margin: auto"
                  >
                    <i class="material-icons">email</i>
                  </a>
                </v-flex>
                <v-flex xs12 md2>
                  <v-text-field
                    v-model="audit[0].npi"
                    label="Provider NPI"
                    disabled
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md2>
                  <v-text-field
                    v-model="audit[0].entity_name"
                    label="Facility"
                    disabled
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md4>
                  <v-select
                    v-model="audit[0].audit_type"
                    :items="auditTypes"
                    label="Audit Type"
                  ></v-select>
                </v-flex>
              </v-layout>
              <v-subheader>Audit Results</v-subheader>
              <v-divider></v-divider>
              <v-layout row wrap>
                <v-flex xs12 md6>
                  <v-checkbox
                    v-model="audit[0].no_charges_found"
                    label="No Charges Found"
                  ></v-checkbox>
                </v-flex>
                <v-flex xs12 md6>
                  <v-checkbox
                    v-model="audit[0].manually_selected_charges"
                    label="Manually Selected Charges"
                  ></v-checkbox>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md6>
                  <v-menu
                    ref="menuDatePickerChargeCorrection"
                    :close-on-content-click="false"
                    v-model="menuDatePickerChargeCorrection"
                    :nudge-right="40"
                    :return-value.sync="audit[0].charge_correction_sent_date"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                  >
                    <v-text-field
                      slot="activator"
                      v-model="audit[0].charge_correction_sent_date"
                      label="Charge Correction Sent Date"
                      prepend-icon="event"
                      readonly
                      data-vv-name="Charge Correction Start Date"
                      :error-messages="
                        errors.collect('Charge Correction Sent Date')
                      "
                    ></v-text-field>
                    <v-date-picker
                      v-model="audit[0].charge_correction_sent_date"
                      @input="
                        $refs.menuDatePickerChargeCorrection.save(
                          audit[0].charge_correction_sent_date
                        )
                      "
                    ></v-date-picker>
                  </v-menu>
                </v-flex>
                <v-flex xs12 md6>
                  <v-layout>
                    <v-flex xs12 md4 pr-1>
                      <v-text-field
                        v-model="audit[0].education_type"
                        label="Communication Type"
                        disabled
                      ></v-text-field>
                    </v-flex>
                    <v-flex xs12 md5 pr-1>
                      <v-text-field
                        v-model="audit[0].education_date"
                        label="Results Communicated Date"
                        disabled
                      ></v-text-field>
                    </v-flex>
                    <v-flex xs12 md3>
                      <v-btn @click="showEducation()">Education ></v-btn>
                    </v-flex>
                  </v-layout>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md3>
                  <v-menu
                    ref="menuDatePickerAuditComplete"
                    :close-on-content-click="false"
                    v-model="menuDatePickerAuditComplete"
                    :nudge-right="40"
                    :return-value.sync="audit[0].audit_completed_date"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                  >
                    <v-text-field
                      slot="activator"
                      v-model="audit[0].audit_completed_date"
                      label="Audit Completed Date"
                      prepend-icon="event"
                      readonly
                      data-vv-name="Audit Completed Date"
                      :error-messages="errors.collect('Audit Completed Date')"
                    ></v-text-field>
                    <v-date-picker
                      v-model="audit[0].audit_completed_date"
                      @input="
                        $refs.menuDatePickerAuditComplete.save(
                          audit[0].audit_completed_date
                        )
                      "
                    ></v-date-picker>
                  </v-menu>
                </v-flex>
                <v-flex xs12 md3>
                  <v-menu
                    ref="menuDatePickerNextAudit"
                    :close-on-content-click="false"
                    v-model="menuDatePickerNextAudit"
                    :nudge-right="40"
                    :return-value.sync="audit[0].next_scheduled_audit_date"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                  >
                    <v-text-field
                      slot="activator"
                      v-model="audit[0].next_scheduled_audit_date"
                      label="Next Scheduled Audit Date"
                      prepend-icon="event"
                      readonly
                      data-vv-name="Next Scheduled Audit Date"
                      :error-messages="
                        errors.collect('Next Scheduled Audit Date')
                      "
                    ></v-text-field>
                    <v-date-picker
                      v-model="audit[0].next_scheduled_audit_date"
                      @input="
                        $refs.menuDatePickerNextAudit.save(
                          audit[0].next_scheduled_audit_date
                        )
                      "
                    ></v-date-picker>
                  </v-menu>
                </v-flex>
                <v-flex xs12 md3>
                  <v-text-field
                    v-model="auditScore"
                    label="Audit Score"
                    disabled
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md3>
                  <v-text-field
                    v-model="auditRisk"
                    label="Audit Risk Level"
                    disabled
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md6>
                  <v-menu
                    ref="menuDatePickerIndividualSummary"
                    :close-on-content-click="false"
                    v-model="menuDatePickerIndividualSummary"
                    :nudge-right="40"
                    :return-value.sync="audit[0].individual_summary_date"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                  >
                    <v-text-field
                      slot="activator"
                      v-model="audit[0].individual_summary_date"
                      label="Individual Summary Date"
                      prepend-icon="event"
                      readonly
                      data-vv-name="Individual Summary Date"
                      :error-messages="
                        errors.collect('Individual Summary Date')
                      "
                    ></v-text-field>
                    <v-date-picker
                      v-model="audit[0].individual_summary_date"
                      @input="
                        $refs.menuDatePickerIndividualSummary.save(
                          audit[0].individual_summary_date
                        )
                      "
                    ></v-date-picker>
                  </v-menu>
                </v-flex>
                <v-flex xs12 md6>
                  <v-text-field
                    v-model="audit[0].pro_auditor_score"
                    label="Pro Auditor Score"
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12>
                  Please upload supporting documentation
                  <FileDragDrop
                    ref="fileDragDrop"
                    @fileList="updateFileList"
                    @fileClicked="previewFile"
                  />
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12>
                  <v-textarea
                    v-model="audit[0].internal_comments"
                    label="Comments (Internal Use Only)"
                    auto-grow
                    box
                  ></v-textarea>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex>
                  <v-btn @click="saveAudit()">Save</v-btn>
                  <v-btn @click="cancelAudit()">Cancel</v-btn>
                  <v-btn v-if="isAdministrator" @click="deleteAudit()"
                    >Delete</v-btn
                  >
                </v-flex>
              </v-layout>
              <v-subheader>
                Encounters
                <v-tooltip bottom>
                  <i
                    class="material-icons"
                    @click="addEncounter()"
                    slot="activator"
                    style="color: #000000"
                    >add_box</i
                  >
                  <span>Create Additional Encounters</span>
                </v-tooltip>
                <v-spacer></v-spacer>
                <v-text-field
                  v-model="encounterFilter"
                  append-icon="search"
                  label="Patient Name Search"
                  single-line
                  hide-details
                ></v-text-field>
              </v-subheader>
              <v-divider></v-divider>
              <v-layout row wrap>
                <v-flex xs12>
                  <v-list>
                    <v-list-tile-title>
                      <v-layout>
                        <v-flex md3>Patient Name</v-flex>
                        <v-flex md3>CPT</v-flex>
                        <v-flex md2>Date of Service</v-flex>
                        <v-flex md2>Status</v-flex>
                        <v-flex md2></v-flex>
                      </v-layout>
                    </v-list-tile-title>
                    <template v-for="(encounter, index) in filteredAudits">
                      <v-list-tile v-if="encounter.patient_name" :key="index">
                        <v-layout class="small-font">
                          <v-flex xs6 md3>{{ encounter.patient_name }}</v-flex>
                          <v-flex xs6 md3>{{ encounter.cpt_billed }}</v-flex>
                          <v-flex xs4 md2>{{ encounter.service_date }}</v-flex>
                          <v-flex xs4 md2 v-if="encounter.audit_completion"
                            >Complete</v-flex
                          >
                          <v-flex xs4 md2 v-else>Pending</v-flex>
                          <v-flex xs4 md2>
                            <v-tooltip bottom>
                              <i
                                class="material-icons"
                                @click="
                                  auditEncounter(encounter.audit_record_id)
                                "
                                slot="activator"
                                >edit</i
                              >
                              <span>View/Edit Encounter</span>
                            </v-tooltip>
                            <v-tooltip bottom>
                              <i
                                class="material-icons"
                                @click="
                                  deleteEncounter(
                                    encounter.audit_record_id,
                                    index
                                  )
                                "
                                slot="activator"
                                >delete</i
                              >
                              <span>Delete Encounter</span>
                            </v-tooltip>
                          </v-flex>
                        </v-layout>
                      </v-list-tile>
                    </template>
                    <v-list-tile v-if="!audit[0].id">
                      <v-layout>
                        <v-flex md12>None</v-flex>
                      </v-layout>
                    </v-list-tile>
                  </v-list>
                </v-flex>
              </v-layout>
            </v-container>
          </v-form>
        </v-card-text>
      </v-card>
      <patient-selection
        id="patientSelectionBlock"
        ref="PatientSelection"
        style="margin-top: 10px"
        v-on:created="init()"
      ></patient-selection>
      <patient-selection-manual
        id="patientSelectionManualBlock"
        ref="PatientSelectionManual"
        style="margin-top: 10px"
      ></patient-selection-manual>
    </v-container>
    <education-dialog
      v-if="showEducationDialog"
      :inPhysicianAuditId="audit[0].id"
      :inPhysicianNpi="audit[0].npi"
      :inPhysicianStartDate="audit[0].provider_start_date"
      @interfaceClose="closeEducation"
    ></education-dialog>
    <confirmation-dialog
      ref="confirmationDialog"
      :title="confirmation.title"
      :message="confirmation.message"
    ></confirmation-dialog>
  </div>
</template>

<script>
import Config from "../config";
import ConfirmationDialog from "@/components/ConfirmationDialog";
import CustomSnackbar from "@/components/Snackbar";
import PatientSelection from "@/components/PatientSelection";
import EducationDialog from "@/components/EducationDialog";
import PatientSelectionManual from "@/components/PatientSelectionManual";
import FileDragDrop from "@/components/FileDragDrop";

//import draggable from "vuedraggable";
import httpService from "../httpService";
import Router from "../router";

// audit_date: new Date().toJSON()
export default {
  name: "Main",
  components: {
    EducationDialog,
    ConfirmationDialog,
    CustomSnackbar,
    PatientSelection,
    PatientSelectionManual,
    FileDragDrop,
  },
  data: () => ({
    encounterFilter: "",
    showPhysicianInfo: true,
    showEducationDialog: false,
    menuDatePickerChargeCorrection: null,
    menuDatePickerAuditComplete: null,
    menuDatePickerNextAudit: null,
    menuDatePickerIndividualSummary: null,
    audit: [
      {
        id: null,
        audit_provider_id: null,
        npi: null,
        provider_name: null,
        entity_name: null,
        audit_date: null,
        charge_correction_sent_date: null,
        audit_completed_date: null,
        no_charges_found: null,
        manually_selected_charges: null,
        audit_record_id: null,
        patient_name: null,
        service_date: null,
        education_audit_id: null,
        education_type: null,
        audit_type: null,
        education_date: null,
        cpt_billed: null,
        encounter_status: null,
        encounter_score: null,
        next_scheduled_audit_date: null,
        individual_summary_date: null,
        pro_auditor_score: null,
        internal_comments: null,
      },
    ],
    confirmation: {
      title: "",
      message: "",
    },
    fileViewerDialog: false,
  }),
  created() {
    // work on this one record
    //this.audit = this.audits.find(elem => elem.audit_id == this.$route.params.audit_id);
    this.init();
  },
  computed: {
    newRecord() {
      return false;
    },
    currentUser() {
      return this.$store.getters.CURRENT_USER;
    },
    auditors() {
      return this.$store.getters.ALL_USERS;
    },
    auditorName() {
      if (this.audit[0].auditor_id >= 0) {
        const indx = this.auditors.findIndex(
          (obj) => obj.id === this.audit[0].auditor_id
        );
        return this.auditors[indx].fullname;
      }
    },
    physicianAuditScore_notused() {
      return this.audit.reduce((acc, val) => {
        return isNaN(val.audit_score) ? acc : acc + val.audit_score;
      }, 0);
    },
    auditTypes() {
      return this.$store.getters.APPLICATION_LISTS.filter(
        (row) => row.keyword === "AUDIT_TYPE"
      ).map((row) => row.value);
    },
    auditScore() {
      let rows = 0;
      let score = 0;

      for (let i = 0; i < this.audit.length; i++) {
        if (this.audit[i].audit_score) {
          score += this.audit[i].audit_score;
        }
        rows++;
      }
      return ((10 * rows - score) / (10 * rows)) * 100;
    },
    filteredAudits() {
      return this.audit.filter((obj) => {
        if (obj.patient_name) {
          return obj.patient_name
            .toUpperCase()
            .includes(this.encounterFilter.toUpperCase());
        }
      });
      // return this.audit.filter(obj => obj.encounter == this.encounterFilter);
    },
    auditRisk() {
      return this.computeRisk(this.auditScore);
    },
    encounterList() {
      // used to pass to patient selection (+manual) component to filter out these encounters
      // 8.28.2020 - switching to patient name + service date because of null encounters
      return this.audit.map((el) => {
        return { ptName: el.patient_name, svcDate: el.service_date };
      });
      // return this.audit.map((el) => parseInt(el.encounter, 10));
    },
    isAdministrator() {
      return this.currentUser.role == "administrator";
    },
    isCompleted() {
      return !this.audit[0].previously_completed_audit;
    },
  },
  methods: {
    async init() {
      console.log("initializing..");
      await httpService.get(
        `${Config.apiRoot}/audit/physician/audit/${this.$route.params.npi}/${this.$route.params.provider_audit_id}`,
        (status, data) => {
          this.audit = data.message;
          // convert the string to array for additional_auditor_id
          for (let i = 0; i < this.audit.length; i++) {
            this.audit[i].additional_auditor_id = this.audit[
              i
            ].additional_auditor_id
              .split(",")
              .map((x) => +x);
          }
          this.$refs.fileDragDrop.files = this.audit[0].files;
        }
      );
    },
    auditEncounter: function (id) {
      // user selected a record to work on
      Router.push({ name: "audit-record", params: { audit_id: id } });
    },
    saveAudit: function () {
      // Always update the 1st record for the physician audit data
      this.$validator.validateAll().then(async (result) => {
        if (!result) {
          return;
        } else {
          // this.audit[0].audit_type = "cancelled"; // WADE not sure why this is here
          let formData = new FormData();
          for (let key in this.audit[0]) {
            if (key != "files") {
              formData.set(key, this.audit[0][key]);
            }
          }
          // deleted blobs
          let tmpDel = [];
          for (var i = 0; i < this.audit[0].files.length; i++) {
            if (
              this.audit[0].files[i].deleted &&
              this.audit[0].files[i].deleted == 1
            ) {
              tmpDel.push(this.audit[0].files[i].id);
            }
          }
          formData.set("deletedBlobs", JSON.stringify(tmpDel));

          for (var i = 0; i < this.audit[0].files.length; i++) {
            formData.append("files[" + i + "]", this.audit[0].files[i]);
          }

          httpService.put(
            `${Config.apiRoot}/audit/physician/audit/${this.audit[0].id}`,
            formData,
            (status, data) => {
              if (data.success) {
                this.showMessage("success", "Record sucessfully saved");
                this.init();
              } else {
                this.showMessage(
                  "error",
                  "There was a problem saving your changes.  MSG - " +
                    data.message.originalError.info.message
                );
              }
            }
          );
        }
      });
    },
    cancelAudit: function () {
      this.confirmation.title = "Cancel Audit?";
      this.confirmation.message =
        "Are you sure you want to cancel your changes?";
      this.$refs.confirmationDialog.confirmation().then((res) => {
        if (res) {
          this.$router.go(-1);
        }
      });
    },
    deleteAudit: function () {
      this.confirmation.title = "Delete Provider's Audit?";
      this.confirmation.message =
        "Are you sure you want to delete this provider's audit?";
      this.$refs.confirmationDialog.confirmation().then((res) => {
        if (res) {
          httpService.delete(
            `${Config.apiRoot}/audit/provider/${this.audit[0].id}`,
            (status, data) => {
              if (data.success) {
                //                this.showMessage("success", "Audit sucessfully deleted");
                this.$router.go(-1);
              } else {
                this.showMessage(
                  "error",
                  "There was a problem deleting the audit.  MSG - " +
                    data.message.originalError.info.message
                );
              }
            }
          );
        }
      });
    },
    popEncounter: function (index) {
      // remove item from the display list
      this.audit.splice(index, 1);
    },
    addEncounter: function () {
      // WADE - manual charges
      this.saveAudit();
      if (this.audit[0].manually_selected_charges) {
        Router.push({
          name: "audit-record",
          params: {
            audit_id: -1,
            npi: this.audit[0].npi,
            provider_audit_id: this.$route.params.provider_audit_id,
            auditor_id: this.audit[0].auditor_id,
          },
        });
      } else {
        // user selected a record to work on
        this.$refs.PatientSelection.showComponent(
          this.audit[0].id,
          this.audit[0].npi,
          this.encounterList
        );
        // scroll to the new object
        document.getElementById("patientSelectionBlock").scrollIntoView(false);
      }
    },
    deleteEncounter: function (id, index) {
      this.confirmation.title = "Delete Encounter?";
      this.confirmation.message =
        "Are you sure you want to delete this encounter?";
      this.$refs.confirmationDialog.confirmation().then((res) => {
        if (res) {
          httpService.delete(
            `${Config.apiRoot}/audit/${id}`,
            (status, data) => {
              if (data.success) {
                this.showMessage("success", "Encounter sucessfully deleted");
                this.popEncounter(index);
              } else {
                this.showMessage(
                  "error",
                  "There was a problem deleting the encounter.  MSG - " +
                    data.message.originalError.info.message
                );
              }
            }
          );
        }
      });
    },
    showMessage: function (type, message) {
      this.$refs.customSnackbar.showSnackbar(message, type, 3000, true);
    },
    showEducation: function (id) {
      this.showPhysicianInfo = false;
      this.showEducationDialog = true;
    },
    closeEducation: function (show, educationDate, educationType) {
      this.showEducationDialog = false;
      this.showPhysicianInfo = true;
      if (educationDate) {
        this.audit[0].education_date = educationDate;
      }
      if (educationType) {
        this.audit[0].education_type = educationType;
      }
    },
    updateFileList(obj) {
      // Updating parent file listing
      this.audit.files = obj;
    },
    previewFile(key) {
      // Create a Blob from the PDF Stream
      this.viewBlob(this.audit.files[key].blob.data);

      // this.$refs.fileViewerDialog.show(
      //   this.audit.files[key].originalname,
      //   `data:application/pdf;base64,${Buffer.from(
      //     this.audit.files[key].blob.data
      //   ).toString("base64")}`
      // );
    },
  },
};
</script>

<style>
i {
  padding: 0 10px;
}
i:hover {
  cursor: pointer;
}
.small-font {
  font-size: 15px;
}
</style>
