<template>
  <v-container>
    <v-layout row wrap>
      <v-flex xs12 sm4 mt-3 mb-3>
        <input
          :disabled="inDisabled"
          type="file"
          id="fileExplorer"
          ref="fileExplorer"
          v-on:change="handleManualFileUpload()"
        />
      </v-flex>
    </v-layout>
    <v-layout row wrap>
      <v-flex xs12 sm4>
        <form id="formDragDrop" ref="fileform" v-if="!inDisabled">
          <span class="drop-files">Drop the files here!</span>
        </form>
      </v-flex>
      <v-flex xs12 sm8>
        <v-data-table
          :headers="tableHeaders"
          :items="activeFiles"
          class="evelation-1"
          :pagination.sync="pagination"
          hide-actions
          no-data-text="No Files"
          loading="false"
        >
          <template v-slot:items="props">
            <tr>
              <td
                v-on:click="fileClicked( props.index )"
                style="cursor:pointer;"
              >{{ props.item.originalname || props.item.name }}</td>
              <td>
                <i
                  class="material-icons"
                  v-on:click="removeFile( props )"
                  style="cursor:pointer;"
                >delete</i>
              </td>
            </tr>
          </template>
        </v-data-table>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
//https://serversideup.net/drag-and-drop-file-uploads-with-vuejs-and-axios/
export default {
  data() {
    return {
      refreshKey: 0,
      tableHeaders: [
        { text: "Filename(s)", value: "name", sortable: false },
        { text: "", value: "", sortable: false },
      ],
      pagination: {
        rowsPerPage: 25,
      },
      dragAndDropCapable: false,
      files: [],
    };
  },
  props: {
    inDisabled: { type: Boolean },
  },
  mounted() {
    // Determine if drag and drop functionality is capable in the browser
    this.dragAndDropCapable = this.determineDragAndDropCapable();

    // If drag and drop capable, then we continue to bind events to our elements.
    if (this.dragAndDropCapable) {
      [
        "drag",
        "dragstart",
        "dragend",
        "dragover",
        "dragenter",
        "dragleave",
        "drop",
      ].forEach(
        function (evt) {
          /*
        For each event add an event listener that prevents the default action
        (opening the file in the browser) and stop the propagation of the event (so
        no other elements open the file in the browser)
      */
          this.$refs.fileform.addEventListener(
            evt,
            function (e) {
              e.preventDefault();
              e.stopPropagation();
            }.bind(this),
            false
          );
        }.bind(this)
      );

      // Add an event listener for drop to the form
      this.$refs.fileform.addEventListener(
        "drop",
        function (e) {
          for (let i = 0; i < e.dataTransfer.files.length; i++) {
            this.files.push(e.dataTransfer.files[i]);
          }
        }.bind(this)
      );
    }
  },
  computed: {
    activeFiles() {
      // return this.files;
      this.refreshKey; // force this to recompute
      return this.files.filter((file) => file.deleted != 1);
    },
  },
  watch: {
    files: {
      handler: function () {
        this.$emit("fileList", this.files);
      },
    },
  },
  methods: {
    handleManualFileUpload() {
      this.files.push(this.$refs.fileExplorer.files[0]);
    },
    determineDragAndDropCapable() {
      var div = document.createElement("div");

      return (
        ("draggable" in div || ("ondragstart" in div && "ondrop" in div)) &&
        "FormData" in window &&
        "FileReader" in window
      );
    },
    removeFile(row) {
      if (row.item.id) {
        const indx = this.files.findIndex((f) => f.id == row.item.id);
        this.files[indx].deleted = 1;
        this.refreshKey++;
        this.$emit("fileList", this.files);
      } else {
        // newly added file that's not in db yet
        const indx = this.files.findIndex((f) => f.name == row.item.name);
        this.files.splice(indx, 1); // NEED to deleted from DB
        this.refreshKey++;
      }
    },
    fileClicked(key) {
      // pass it up to the parent to handle
      this.$emit("fileClicked", key);
    },
    reset() {
      this.files = [];
    },
  },
};
</script>

<style>
#formDragDrop {
  display: block;
  height: 150px;
  width: 150px;
  background: #ccc;
  margin: auto;
  text-align: center;
  line-height: 150px;
  border-radius: 4px;
}

table.v-table111 thead tr {
  height: 40px !important;
}
</style>