<template>
  <div style="margin-top:15px;">
    <v-card tile>
      <v-card-title class="headline font-weight-regular chsli-color white--text">Editing Education</v-card-title>
      <v-card-text>
        <v-container>
          <v-layout row wrap>
            <v-card width="100%" class="sub-card">
              <v-container fluid grid-list-md>
                <v-layout row wrap>
                  <v-flex xs12 md4 pr-1>
                    <v-text-field v-model="computedPhysicianName" label="Physician Name" disabled></v-text-field>
                  </v-flex>
                  <v-flex xs12 md4 pr-1>
                    <v-text-field v-model="computedAuditorName" label="Auditor" disabled></v-text-field>
                  </v-flex>
                  <v-flex xs12 md4>
                    <v-select
                      v-model="education.education_performed_by_id"
                      :items="auditors"
                      item-text="fullname"
                      item-value="id"
                      label="Education Performed By"
                    ></v-select>
                  </v-flex>
                </v-layout>
                <v-layout row wrap>
                  <v-flex xs12 md4 pr-1>
                    <v-select
                      v-model="education.type"
                      :items="educationTypes"
                      label="Type of Education"
                    ></v-select>
                  </v-flex>
                  <v-flex xs12 md2 pr-1>
                    <v-select v-model="education.method" :items="educationMethods" label="Method"></v-select>
                  </v-flex>
                  <v-flex xs12 md3 pr-1>
                    <v-menu
                      ref="menuDateToday"
                      :close-on-content-click="false"
                      v-model="menuDateToday"
                      :nudge-right="40"
                      :return-value.sync="education.education_date"
                      lazy
                      transition="scale-transition"
                      offset-y
                      full-width
                      min-width="290px"
                    >
                      <v-text-field
                        slot="activator"
                        v-model="education.education_date"
                        label="Education Date"
                        prepend-icon="event"
                        readonly
                        lazy
                        required
                        data-vv-name="Education Date"
                        v-validate="'required'"
                        :error-messages="errors.collect('Education Date')"
                      ></v-text-field>
                      <v-date-picker
                        v-model="education.education_date"
                        @input="$refs.menuDateToday.save(education.education_date)"
                      ></v-date-picker>
                    </v-menu>
                  </v-flex>
                  <v-flex xs12 md3>
                    <v-menu
                      ref="menuDateStart"
                      :close-on-content-click="false"
                      v-model="menuDateStart"
                      :nudge-right="40"
                      :return-value.sync="education.start_date"
                      lazy
                      transition="scale-transition"
                      offset-y
                      full-width
                      min-width="290px"
                      v-if="education.type != 'In Service' && education.type != 'Remedial'"
                    >
                      <v-text-field
                        slot="activator"
                        v-model="education.start_date"
                        label="Start Date (if applicable)"
                        prepend-icon="event"
                        readonly
                      ></v-text-field>
                      <v-date-picker
                        v-model="education.start_date"
                        @input="$refs.menuDateStart.save(education.start_date)"
                      ></v-date-picker>
                    </v-menu>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-card>
          </v-layout>

          <v-layout row wrap pt-2>
            <v-card width="100%" class="sub-card">
              <v-card-title class="sub-header">
                <v-checkbox
                  label="Evaluation and Management (E/M) Services"
                  v-model="education.emservices"
                  ma-0
                  pa-0
                  :disabled="isMethodEmail"
                ></v-checkbox>
              </v-card-title>
              <v-container fluid v-if="education.emservices">
                <v-layout row wrap>
                  <v-flex xs12 md4>
                    <v-checkbox
                      label="Chief Complaint"
                      v-model="education.emservices_chief_complaint"
                      @change="setEMServices()"
                      ma-0
                      pa-0
                      :disabled="isMethodEmail"
                    ></v-checkbox>
                    <v-checkbox
                      label="HPI"
                      v-model="education.emservices_hpi"
                      :disabled="isMethodEmail"
                    ></v-checkbox>
                    <v-checkbox
                      label="ROS"
                      v-model="education.emservices_ros"
                      :disabled="isMethodEmail"
                    ></v-checkbox>
                    <v-checkbox
                      label="PFSH"
                      v-model="education.emservices_pfsh"
                      :disabled="isMethodEmail"
                    ></v-checkbox>
                  </v-flex>
                  <v-flex xs12 md4>
                    <v-layout row>
                      <v-flex xs12 md4>
                        <v-checkbox
                          label="PE"
                          v-model="education.emservices_pe"
                          :disabled="isMethodEmail"
                        ></v-checkbox>
                      </v-flex>
                      <v-flex xs12 md4>
                        <v-checkbox
                          label="1995"
                          v-model="education.emservices_1995"
                          :disabled="isMethodEmail"
                        ></v-checkbox>
                      </v-flex>
                      <v-flex xs12 md4>
                        <v-checkbox
                          label="1997"
                          v-model="education.emservices_1997"
                          :disabled="isMethodEmail"
                        ></v-checkbox>
                      </v-flex>
                    </v-layout>
                    <v-checkbox
                      label="MDM"
                      v-model="education.emservices_mdm"
                      :disabled="isMethodEmail"
                    ></v-checkbox>
                    <v-checkbox
                      label="Counseling and coordination of care > 50% of encounter"
                      v-model="education.emservices_counseling"
                      :disabled="isMethodEmail"
                    ></v-checkbox>
                  </v-flex>
                  <v-flex xs12 md4 pr-2>
                    <v-checkbox
                      label="PowerPoint presentations provided as f/u via email"
                      v-model="education.aaemservices_pptx_providedaa"
                      :disabled="isMethodEmail"
                    ></v-checkbox>
                    <v-text-field
                      label="Free Text Box 1"
                      v-model="education.emservices_other1_text"
                      :disabled="isMethodEmail"
                    ></v-text-field>
                    <v-text-field
                      label="Free Text Box 2"
                      v-model="education.emservices_other2_text"
                      :disabled="isMethodEmail"
                    ></v-text-field>
                    <v-text-field
                      label="Free Text Box 3"
                      v-model="education.emservices_other3_text"
                      :disabled="isMethodEmail"
                    ></v-text-field>
                    <v-checkbox
                      label="E/M Coding cards provided"
                      v-model="education.emservices_cards_provided"
                      :disabled="isMethodEmail"
                    ></v-checkbox>
                  </v-flex>
                </v-layout>
                <v-layout>
                  <v-flex xs12 md6>
                    <v-checkbox
                      label="Advanced Care Planning"
                      v-model="education.emservices_advanced_care_planning"
                      :disabled="isMethodEmail"
                    ></v-checkbox>
                    <v-checkbox
                      label="Chronic Care Management"
                      v-model="education.emservices_chronic_care_management"
                      :disabled="isMethodEmail"
                    ></v-checkbox>
                    <v-checkbox
                      label="Medicare Annual Wellness Visit"
                      v-model="education.emservices_medicare_wellness_visit"
                      :disabled="isMethodEmail"
                    ></v-checkbox>
                    <v-checkbox
                      label="New vs Established Patient Visists"
                      v-model="education.emservices_new_established_visits"
                      :disabled="isMethodEmail"
                    ></v-checkbox>
                    <v-checkbox
                      label="Observation Care"
                      v-model="education.emservices_observation_care"
                      :disabled="isMethodEmail"
                    ></v-checkbox>
                    <v-checkbox
                      label="Other Services:"
                      v-model="education.emservices_other_services"
                      :disabled="isMethodEmail"
                    ></v-checkbox>
                  </v-flex>
                  <v-flex xs12 md6>
                    <v-checkbox
                      label="Critical Care - Time Based"
                      v-model="education.emservices_critical_care"
                      :disabled="isMethodEmail"
                    ></v-checkbox>
                    <v-checkbox
                      label="Home Visits"
                      v-model="education.emservices_home_visits"
                      :disabled="isMethodEmail"
                    ></v-checkbox>
                    <v-checkbox
                      label="Medicare Initial Preventive Physician Examination"
                      v-model="education.emservices_medicare_initial_exam"
                      :disabled="isMethodEmail"
                    ></v-checkbox>
                    <v-checkbox
                      label="Nursing Facility"
                      v-model="education.emservices_nursing_facility"
                      :disabled="isMethodEmail"
                    ></v-checkbox>
                    <v-checkbox
                      label="Transitional Care Management"
                      v-model="education.emservices_transitional_management"
                      :disabled="isMethodEmail"
                    ></v-checkbox>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-card>
          </v-layout>

          <v-layout row wrap pt-2>
            <v-flex xs12 md6 pr-1>
              <v-card width="100%" class="sub-card">
                <v-card-title class="sub-header">
                  <v-checkbox
                    label="CMS Incident-To Criteria"
                    v-model="education.incidentto"
                    :disabled="isMethodEmail"
                  ></v-checkbox>
                </v-card-title>
                <v-container fluid v-if="education.incidentto">
                  <v-layout row wrap>
                    <v-flex xs12 md4>
                      <v-checkbox
                        label="Direct Supervision"
                        v-model="education.incidentto_direct_supervision"
                        :disabled="isMethodEmail"
                      ></v-checkbox>
                      <v-checkbox
                        label="Employed/Leased"
                        v-model="education.incidentto_employed"
                        :disabled="isMethodEmail"
                      ></v-checkbox>
                      <v-checkbox
                        label="No new patients/problems"
                        v-model="education.incidentto_patients_problems"
                        :disabled="isMethodEmail"
                      ></v-checkbox>
                    </v-flex>
                  </v-layout>
                </v-container>
              </v-card>
            </v-flex>
          </v-layout>

          <v-layout row wrap pt-2>
            <v-flex xs12 md6>
              <v-card width="100%" class="sub-card">
                <v-card-title class="sub-header">
                  <v-checkbox
                    label="CMS Split/Shared Services"
                    v-model="education.sharedservices"
                    :disabled="isMethodEmail"
                  ></v-checkbox>
                </v-card-title>
              </v-card>
            </v-flex>
          </v-layout>

          <v-layout row wrap pt-2>
            <v-flex xs12 md6 pr-1>
              <v-card width="100%" class="sub-card">
                <v-card-title class="sub-header">
                  <v-checkbox
                    label="Non-physician (MLP) Direct Billing"
                    v-model="education.mlp_direct_billing"
                    :disabled="isMethodEmail"
                  ></v-checkbox>
                </v-card-title>
              </v-card>
            </v-flex>
          </v-layout>

          <v-layout row wrap pt-2>
            <v-flex xs12>
              <v-card width="100%" class="sub-card">
                <v-card-title class="sub-header">
                  <v-checkbox
                    label="CMS Policies"
                    v-model="education.policies"
                    :disabled="isMethodEmail"
                  ></v-checkbox>
                </v-card-title>
                <v-container fluid v-if="education.policies">
                  <v-layout row wrap>
                    <v-flex xs12 md6>
                      <v-checkbox
                        label="Addendums"
                        v-model="education.policies_addendums"
                        :disabled="isMethodEmail"
                      ></v-checkbox>
                      <v-checkbox
                        label="CHS Provider Coding and Documentation Review Policy"
                        v-model="education.policies_review_policy"
                        :disabled="isMethodEmail"
                      ></v-checkbox>
                      <v-checkbox
                        label="Other Policy:"
                        v-model="education.policies_other"
                        :disabled="isMethodEmail"
                      ></v-checkbox>
                    </v-flex>
                    <v-flex xs12 md6>
                      <v-checkbox
                        label="Copy/Paste"
                        v-model="education.policies_copy_paste"
                        :disabled="isMethodEmail"
                      ></v-checkbox>
                      <v-checkbox
                        label="Scribe Services in Ambulatory Practices"
                        v-model="education.policies_scribe_services"
                        :disabled="isMethodEmail"
                      ></v-checkbox>
                    </v-flex>
                  </v-layout>
                </v-container>
              </v-card>
            </v-flex>
          </v-layout>

          <v-layout row wrap pt-2>
            <v-flex xs12 md6 pr-1>
              <v-card width="100%" class="sub-card">
                <v-card-title class="sub-header">
                  <v-checkbox
                    label="ICD-10-CM Coding Guidelines"
                    v-model="education.coding_guidelines"
                    :disabled="isMethodEmail"
                  ></v-checkbox>
                </v-card-title>
              </v-card>
            </v-flex>
          </v-layout>
          <v-layout row wrap pt-2>
            <v-flex xs12 md6>
              <v-card width="100%" class="sub-card">
                <v-card-title class="sub-header">
                  <v-checkbox
                    label="CMS Global Surgical Rules"
                    v-model="education.surgical_rules"
                    :disabled="isMethodEmail"
                  ></v-checkbox>
                </v-card-title>
                <v-container fluid v-if="education.surgical_rules">
                  <v-layout row wrap>
                    <v-flex xs12 md6>
                      <v-checkbox
                        label="Fact sheet sent via email"
                        v-model="education.surgical_rules_fact_sheet"
                        :disabled="isMethodEmail"
                      ></v-checkbox>
                    </v-flex>
                  </v-layout>
                </v-container>
              </v-card>
            </v-flex>
          </v-layout>

          <v-layout row wrap pt-2>
            <v-flex xs12>
              <v-card width="100%" class="sub-card">
                <v-card-title class="sub-header">
                  <v-checkbox
                    label="Modifiers"
                    v-model="education.modifiers"
                    :disabled="isMethodEmail"
                  ></v-checkbox>
                </v-card-title>
                <v-container fluid v-if="education.modifiers">
                  <v-layout row wrap>
                    <v-flex xs12 md2>
                      <v-checkbox
                        label="24"
                        v-model="education.modifiers_24"
                        :disabled="isMethodEmail"
                      ></v-checkbox>
                      <v-checkbox
                        label="80, 81, 82"
                        v-model="education.modifiers_80_81_82"
                        :disabled="isMethodEmail"
                      ></v-checkbox>
                    </v-flex>
                    <v-flex xs12 md2>
                      <v-checkbox
                        label="25"
                        v-model="education.modifiers_25"
                        :disabled="isMethodEmail"
                      ></v-checkbox>
                      <v-checkbox
                        label="AS"
                        v-model="education.modifiers_as"
                        :disabled="isMethodEmail"
                      ></v-checkbox>
                    </v-flex>
                    <v-flex xs12 md2>
                      <v-checkbox
                        label="50, RT, LT"
                        v-model="education.modifiers_50_rt_lt"
                        :disabled="isMethodEmail"
                      ></v-checkbox>
                      <v-checkbox
                        label="A1"
                        v-model="education.modifiers_a1"
                        :disabled="isMethodEmail"
                      ></v-checkbox>
                    </v-flex>
                    <v-flex xs12 md2>
                      <v-checkbox
                        label="57"
                        v-model="education.modifiers_57"
                        :disabled="isMethodEmail"
                      ></v-checkbox>
                      <v-checkbox
                        label="Other Modifier(s):"
                        v-model="education.modifiers_other"
                        :disabled="isMethodEmail"
                      ></v-checkbox>
                    </v-flex>
                    <v-flex xs12 md2>
                      <v-checkbox
                        label="59"
                        v-model="education.modifiers_59"
                        :disabled="isMethodEmail"
                      ></v-checkbox>
                    </v-flex>
                    <v-flex xs12 md2>
                      <v-checkbox
                        label="62"
                        v-model="education.modifiers_62"
                        :disabled="isMethodEmail"
                      ></v-checkbox>
                    </v-flex>
                  </v-layout>
                </v-container>
              </v-card>
            </v-flex>
          </v-layout>

          <v-layout row wrap pt-2>
            <v-flex xs12>
              <v-card width="100%" class="sub-card">
                <v-card-title class="sub-header">
                  <v-checkbox
                    label="Teaching Physician Rules"
                    v-model="education.teaching_rules"
                    :disabled="isMethodEmail"
                  ></v-checkbox>
                </v-card-title>
              </v-card>
            </v-flex>
          </v-layout>

          <v-layout row wrap pt-2>
            <v-flex xs12 md6 pr-1>
              <v-card width="100%" class="sub-card">
                <v-card-title class="sub-header">
                  <v-checkbox
                    label="Charge Ticket Review"
                    v-model="education.ticket_review"
                    :disabled="isMethodEmail"
                  ></v-checkbox>
                </v-card-title>
              </v-card>
            </v-flex>
          </v-layout>
          <v-layout row wrap pt-2>
            <v-flex xs12 md6>
              <v-card width="100%" class="sub-card">
                <v-card-title class="sub-header">
                  <v-checkbox
                    label="Consultations: Request from another provider/Reason Letter to requesting provider"
                    v-model="education.consulations"
                    :disabled="isMethodEmail"
                  ></v-checkbox>
                </v-card-title>
              </v-card>
            </v-flex>
          </v-layout>

          <v-layout row wrap pt-2>
            <v-flex xs12>
              <v-card width="100%" class="sub-card">
                <v-card-title class="sub-header" style="height:50px:padding:0">
                  <v-checkbox label="Questions/Comments:" v-model="education.questions"></v-checkbox>
                </v-card-title>
                <v-container fluid>
                  <v-layout row>
                    <v-flex xs12>
                      <v-textarea v-model="education.questions_text" rows="3"></v-textarea>
                    </v-flex>
                  </v-layout>
                </v-container>
              </v-card>
            </v-flex>
          </v-layout>
          <v-layout row wrap mt-4>
            <v-flex xs12 sm8 md4>
              <v-btn @click="saveEducation()">Save</v-btn>
              <v-btn @click="cancelEducation()">Cancel</v-btn>
            </v-flex>
            <v-flex xs12 sm4 md2>
              <v-btn @click="deleteEducation()">Delete</v-btn>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card-text>
    </v-card>
    <custom-snackbar ref="customSnackbar"></custom-snackbar>
    <confirmation-dialog
      ref="confirmationDialog"
      :title="confirmComponentMessage.title"
      :message="confirmComponentMessage.message"
    ></confirmation-dialog>
  </div>
</template>

<script>
import ConfirmationDialog from "@/components/ConfirmationDialog";
import CustomSnackbar from "@/components/Snackbar";
import Config from "../config";
import httpService from "../httpService";
import { Validator } from "vee-validate";

export default {
  name: "dialogEducation",
  components: {
    ConfirmationDialog,
    CustomSnackbar,
  },
  data: function () {
    return {
      menuDateToday: false,
      menuDateStart: false,
      confirmComponentMessage: { title: "", message: "" },
      npi: null,
      auditId: this.inPhysicianAuditId,
      education: null,
      storedEducationDate: null,
      storedEducationType: null,
    };
  },
  props: {
    inPhysicianNpi: { type: String },
    inPhysicianStartDate: { type: String },
    inPhysicianAuditId: { type: Number, default: -1 },
    inIsRowId: { type: String, default: "0" },
  },
  beforeMount() {},
  created() {
    if (this.inPhysicianNpi) {
      this.npi = this.inPhysicianNpi;
    } else {
      this.npi = this.$parent.selectedNpi;
    }

    this.education = initialEducation(this.inPhysicianStartDate);
    let url = "";
    // one uses the db row id, one uses the audit id (when called from the audit record)
    if (this.inIsRowId === "1") {
      url = `${Config.apiRoot}/physician/education/auditById/${this.auditId}`;
    } else {
      url = `${Config.apiRoot}/physician/education/audit/${this.auditId}`;
    }

    // fetch education record for this audit
    httpService.get(url, (status, data) => {
      if (data[0]) {
        // only if there's a row
        this.education = data[0];
      } else {
        // const today = new Date();
        // this.education.start_date = `${today.getFullYear()}-${
        //   today.getMonth() + 1
        // }-${today.getDate()}`;
        this.education.auditor_id = this.$store.getters.CURRENT_USER.id;
      }
    });
  },
  watch: {
    "education.method": function (newVal, oldVal) {
      if (newVal && newVal.toLowerCase() === "email") {
        // clear education fields
        this.education.emservices = null;
        this.education.emservices_chief_complaint = null;
        this.education.emservices_hpi = null;
        this.education.emservices_ros = null;
        this.education.emservices_pfsh = null;
        this.education.emservices_pe = null;
        this.education.emservices_1995 = null;
        this.education.emservices_1997 = null;
        this.education.emservices_mdm = null;
        this.education.emservices_counseling = null;
        this.education.emservices_pptx_provided = null;
        this.education.emservices_cards_provided = null;
        this.education.emservices_other1 = null;
        this.education.emservices_other2 = null;
        this.education.emservices_other3 = null;
        this.education.emservices_other1_text = null;
        this.education.emservices_other2_text = null;
        this.education.emservices_other3_text = null;
        this.education.emservices_advanced_care_planning = null;
        this.education.emservices_chronic_care_management = null;
        this.education.emservices_medicare_wellness_visit = null;
        this.education.emservices_new_established_visits = null;
        this.education.emservices_observation_care = null;
        this.education.emservices_critical_care = null;
        this.education.emservices_home_visits = null;
        this.education.emservices_medicare_initial_exam = null;
        this.education.emservices_nursing_facility = null;
        this.education.emservices_transitional_management = null;
        this.education.emservices_other_services = null;
        this.education.emservices_other_services_text = null;
        this.education.incidentto = null;
        this.education.incidentto_direct_supervision = null;
        this.education.incidentto_employed = null;
        this.education.incidentto_patients_problems = null;
        this.education.sharedservices = null;
        this.education.mlp_direct_billing = null;
        this.education.policies = null;
        this.education.policies_addendums = null;
        this.education.policies_review_policy = null;
        this.education.policies_copy_paste = null;
        this.education.policies_scribe_services = null;
        this.education.policies_other = null;
        this.education.policies_other_text = null;
        this.education.coding_guidelines = null;
        this.education.surgical_rules = null;
        this.education.surgical_rules_fact_sheet = null;
        this.education.modifiers = null;
        this.education.modifiers_24 = null;
        this.education.modifiers_25 = null;
        this.education.modifiers_50_rt_lt = null;
        this.education.modifiers_57 = null;
        this.education.modifiers_59 = null;
        this.education.modifiers_62 = null;
        this.education.modifiers_80_81_82 = null;
        this.education.modifiers_as = null;
        this.education.modifiers_a1 = null;
        this.education.modifiers_other = null;
        this.education.modifiers_other_text = null;
        this.education.teaching_rules = null;
        this.education.ticket_review = null;
        this.education.consulations = null;
        // this.education.questions = null;
        // this.education.questions_text = null;
      }
    },
  },
  computed: {
    auditors() {
      return this.$store.getters.ALL_USERS;
    },
    computedPhysicianName() {
      const tmpPhys = this.$store.getters.PHYSICIAN.filter(
        (row) => row.npi === this.inPhysicianNpi
      );
      return tmpPhys[0].last_name + ", " + tmpPhys[0].first_name;
    },
    computedAuditorName() {
      if (this.education.auditor_id) {
        return this.$store.getters.ALL_USERS.filter(
          (row) => row.id === this.education.auditor_id
        )[0].fullname;
      }
    },
    isMethodEmail() {
      // if email method selected, disable input for rest of form
      if (this.education.method) {
        return this.education.method.toLowerCase() === "email";
      }
    },
    educationTypes() {
      return this.$store.getters.APPLICATION_LISTS.filter(
        (row) => row.keyword === "EDUCATION_TYPE"
      ).map((row) => row.value);
    },
    educationMethods() {
      return this.$store.getters.APPLICATION_LISTS.filter(
        (row) => row.keyword === "EDUCATION_METHOD"
      ).map((row) => row.value);
    },
    isEMServicesSelected() {
      return (
        this.education.emservices_chief_complaint ||
        this.education.emservices_hpi ||
        this.education.emservices_ros ||
        this.education.emservices_pfsh ||
        this.education.emservices_pe ||
        this.education.emservices_1995 ||
        this.education.emservices_1997 ||
        this.education.emservices_mdm ||
