  methods: {
    cancelEducation: function () {
      this.confirmComponentMessage.title = "Cancel?";
      this.confirmComponentMessage.message =
        "Are you sure you want to cancel your changes?";
      this.$refs.confirmationDialog.confirmation().then((res) => {
        if (res) {
          this.$emit("interfaceClose", false);
        }
      });
    },
    showMessage: function (type, message) {
      this.$refs.customSnackbar.showSnackbar(message, type, 3000, false);
    },
    saveEducation: async function () {
      this.$validator.validateAll().then(async (result) => {
        if (!result) {
          return;
        } else {
          this.education.physician_audit_id = this.auditId; // set the key
          this.education.npi = this.npi; // set the npi
          httpService.post(
            `${Config.apiRoot}/physician/education`,
            this.education,
            async (status, data) => {
              if (data.success && data.rowsAffected == 1) {
                // only hide if successfull
                this.education.id = data.new_id; // add new id first
                // this.storedEducationDate = this.education.education_date; // used to pass back to parent (only update if data is saved)
                // this.storedEducationType = this.education.education_type; // used to pass back to parent (only update if data is saved)
                this.showMessage(
                  "success",
                  "Education has been added successfully"
                );
                await this.sleep(3000);
                this.$emit(
                  "interfaceClose",
                  false,
                  this.education.education_date,
                  this.education.type
                );
              } else {
                console.log("Saving Education Error - ", JSON.stringify(data));
                // data.message.originalError.info.message
                this.showMessage(
                  "error",
                  "There was a problem saving your changes.  MSG - " + data
                );
              }
            }
          );
        }
      });
    },
    deleteEducation: async function () {
      this.confirmComponentMessage.title = "Confirm Deletion?";
      this.confirmComponentMessage.message =
        "Are you sure you want to delete this record?";
      this.$refs.confirmationDialog
        .confirmation()
        .then((res) => {
          if (res) {
            httpService.delete(
              `${Config.apiRoot}/physician/education/${this.education.id}`,
              async (status, data) => {
                if (data.success && data.message.rowsAffected == 1) {
                  // only hide if successfull
                  this.showMessage("success", "Record sucessfully deleted");
                  await this.sleep(3000);
                  this.$emit("interfaceClose", false);
                } else {
                  this.showMessage(
                    "error",
                    "There was a problem sdeleting the record.  MSG - " + err
                  );
                }
              }
            );
          }
        })
        .catch((err) => {
          console.log("err - ", err);
          this.showMessage(
            "error",
            "There was a problem sdeleting the record.  MSG - " + err
          );
        });
    },
  },
};

