<template>
  <div>
    <v-container>
      <custom-snackbar ref="customSnackbar"></custom-snackbar>
      <v-card class="mb-2">
        <v-card-title
          class="headline font-weight-regular chsli-color white--text"
        >Entity Maintenance</v-card-title>
        <v-card-text>
          <v-subheader class="pa-0">
            Entity Lookup
            <i
              v-if="isAdministrator"
              class="material-icons"
              @click="newEntity()"
              style="padding-left:8px;"
            >add_box</i>
          </v-subheader>
          <v-autocomplete
            v-model="selectedId"
            :items="entities"
            persistent-hint
            item-text="entity_name"
            item-value="id"
            @change="selectEntity(selectedId)"
          ></v-autocomplete>
        </v-card-text>
      </v-card>
      <v-card v-if="showEntityProfile">
        <v-card-title class="headline font-weight-regular chsli-color white--text">Entity Profile</v-card-title>
        <v-card-text>
          <v-form ref="form">
            <v-text-field v-model="entity.action" label="Action Type" v-show="false"></v-text-field>
            <v-container fluid>
              <v-layout row wrap>
                <v-flex xs12 md6>
                  <v-text-field
                    v-model="entity.entity_name"
                    v-validate="'required'"
                    data-vv-name="Name"
                    label="Name"
                    required
                    :error-messages="errors.collect('Name')"
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md6>
                  <v-text-field
                    v-model="entity.chief_administrative_officer"
                    data-vv-name="Chief Administrative Officer"
                    label="Chief Administrative Officer"
                    :error-messages="errors.collect('Chief Administrative Officer')"
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md6>
                  <v-text-field
                    v-model="entity.chief_compliance_officer"
                    data-vv-name="Chief Compliance Officer"
                    label="Chief Compliance Officer"
                    :error-messages="errors.collect('Chief Compliance Officer')"
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md6>
                  <v-text-field
                    v-model="entity.chief_medical_officer"
                    data-vv-name="Chief Medical Officer"
                    label="Chief Medical Officer"
                    :error-messages="errors.collect('Chief Medical Officer')"
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 sm8 md4>
                  <v-btn v-if="isAdministrator" @click="saveEntity()">Save</v-btn>
                  <v-btn @click="cancelEntity()">Cancel</v-btn>
                </v-flex>
                <v-flex xs12 sm4 md2>
                  <v-btn v-if="isAdministrator" @click="deleteEntity()">Delete</v-btn>
                </v-flex>
              </v-layout>
            </v-container>
          </v-form>
        </v-card-text>
      </v-card>
    </v-container>
    <confirmation-dialog
      ref="confirmationDialog"
      :title="confirmComponentMessage.title"
      :message="confirmComponentMessage.message"
    ></confirmation-dialog>

    <!-- <pre>{{ currentUser }}</pre>
    <pre>{{ audits }}</pre>-->
  </div>
</template>

<script>
import ConfirmationDialog from "@/components/ConfirmationDialog";
import CustomSnackbar from "@/components/Snackbar";

const initializeEntity = () => {
  return {
    action: "",
    id: "",
    entity_name: "",
    chief_administrative_officer: "",
    chief_compliance_officer: "",
    chief_medical_officer: "",
  };
};

export default {
  name: "Main",
  components: {
    ConfirmationDialog,
    CustomSnackbar,
  },
  data: () => ({
    entity: {},
    selectedId: null,
    showEntityProfile: false,
    confirmComponentMessage: { title: "", message: "" },
  }),
  computed: {
    entities() {
      return this.$store.getters.ENTITY;
    },
    currentUser() {
      return this.$store.getters.CURRENT_USER;
    },
    isAdministrator() {
      return this.currentUser.role === "administrator";
    },
  },
  methods: {
    selectEntity: function (id) {
      if (id) {
        this.entity = Object.assign(
          {},
          this.entities.find((p) => p.id === id)
        );
        this.entity.action = "update"; // updating an existing entity
        this.showEntityProfile = true;
      }
    },
    newEntity: function () {
      this.entity = initializeEntity();
      this.entity.action = "new";
      this.showEntityProfile = true;
    },
    saveEntity: function () {
      this.$validator.validateAll().then((result) => {
        if (!result) {
          return false;
        } else {
          let dispatchType = "";
          if (this.entity.action === "new") {
            dispatchType = "NEW_ENTITY";
          } else {
            dispatchType = "UPDATE_ENTITY";
          }
          // add role
          this.entity.userRole = this.currentUser.role;
          this.$store
            .dispatch(dispatchType, this.entity)
            .then((data) => {
              if (data === true) {
                this.showMessage("success", "Record sucessfully saved");
                if (this.entity.action === "new") {
                  this.entity = initializeEntity(); // re-initialize fields
                }
              } else {
                this.showMessage(
                  "error",
                  "There was a problem saving your changes.  MSG - " + data
                );
              }
            })
            .catch((err) => {
              console.log("err - ", err);
              this.showMessage(
                "error",
                "There was a problem saving your changes.  MSG - " + err
              );
            });
        }
      });
    },
    deleteEntity: function () {
      this.confirmComponentMessage.title = "Confirm Deletion?";
      this.confirmComponentMessage.message =
        "Are you sure you want to delete this record?";
      this.$refs.confirmationDialog
        .confirmation()
        .then((res) => {
          if (res) {
            this.showEntityProfile = false;
            // add role
            this.entity.userRole = this.currentUser.role;
            this.$store.dispatch("DELETE_ENTITY", this.entity).then((data) => {
              if (data === true) {
                this.showMessage("success", "Record sucessfully deleted");
                this.selectedId = null;
                this.showEntityProfile = false;
              } else {
                this.showMessage(
                  "error",
                  "There was a problem deleting the record.  MSG - " + data
                );
              }
            });
          }
        })
        .catch((err) => {
          console.log("err - ", err);
          this.showMessage(
            "error",
            "There was a problem sdeleting the record.  MSG - " + err
          );
        });
    },
    cancelEntity: function () {
      this.confirmComponentMessage.title = "Cancel?";
      this.confirmComponentMessage.message =
        "Are you sure you want to cancel your changes?";
      this.$refs.confirmationDialog.confirmation().then((res) => {
        if (res) {
          this.selectedId = null;
          this.showEntityProfile = false;
        }
      });
    },
    showMessage: function (type, message) {
      this.$refs.customSnackbar.showSnackbar(message, type, 3000, false);
    },
  },
};
</script>

<style>
i:hover {
  cursor: pointer;
}
</style>
