<template>
  <div>
    <v-container>
      <v-card>
        <v-card-title
          class="headline font-weight-regular chsli-color white--text"
        >User Logging Trail</v-card-title>
        <v-layout>
          <v-flex xs6 md3 ml-2>
            <v-checkbox v-model="dataTableFilter" label="Selects" value="GET"></v-checkbox>
          </v-flex>
          <v-flex xs6 md3>
            <v-checkbox v-model="dataTableFilter" label="Updates" value="PUT"></v-checkbox>
          </v-flex>
          <v-flex xs6 md3>
            <v-checkbox v-model="dataTableFilter" label="Inserts" value="POST"></v-checkbox>
          </v-flex>
          <v-flex xs6 md3>
            <v-checkbox v-model="dataTableFilter" label="Deletions" value="DEL"></v-checkbox>
          </v-flex>
        </v-layout>
        <v-data-table
          :headers="tableHeaders"
          :items="filteredItems"
          class="evelation-1"
          :pagination.sync="pagination"
          :expand="expand"
        >
          <template v-slot:items="props">
            <tr @click="props.expanded = !props.expanded" style="cursor:pointer;">
              <td>{{ props.item.id }}</td>
              <td>{{ props.item.timestamp }}</td>
              <td>{{ props.item.method }}</td>
              <td>{{ props.item.url }}</td>
              <td>{{ props.item.username }}</td>
            </tr>
          </template>
          <template v-slot:expand="props">
            <v-card flat>
              <v-card-text>
                <pre>{{ JSON.parse(props.item.body) }}</pre>
              </v-card-text>
            </v-card>
          </template>
        </v-data-table>
      </v-card>
    </v-container>
  </div>
</template>

<script>
import Config from "../config";
import httpService from "../httpService";

export default {
  name: "Main",
  data: () => ({
    logs: [],
    expand: false,
    tableHeaders: [
      { text: "ID", value: "id" },
      { text: "Date/Time", value: "timestamp" },
      { text: "Method", value: "method" },
      { text: "URL", value: "url" },
      { text: "User", value: "username" }
    ],
    pagination: {
      rowsPerPage: 25,
      sortBy: "id",
      descending: true
    },
    dataTableFilter: ["DEL", "POST", "PUT"]
  }),
  mounted() {
    // get log data
    httpService.get(`${Config.apiRoot}/utils/_logging`, (status, data) => {
      this.logs = data;
    });
  },
  computed: {
    filteredItems() {
      return this.logs.filter(el => this.dataTableFilter.includes(el.method));
    }
  },
  methods: {
    showLog: function(id) {
      this.educationIndex = id;
      this.showEducationDialog = true;
    }
  }
};
</script>

<style>
i:hover {
  cursor: pointer;
}
</style>
 