<template>
  <v-dialog v-model="showDialog">
    <v-card style="height: 90vh; width: 100%">
      <v-card-title class="justify-center">
        {{ title }}
        <v-spacer></v-spacer>
        <v-btn color="success" @click="close()">Close</v-btn>
      </v-card-title>
      <v-card-text
        :key="xkey"
        id="fileViewerParent"
        style="height: 90%; width: 100%"
      >
        <embed id="fileViewer" style="height: 100%; width: 100%" />
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: "fileViewerDialog",
  data: function () {
    return {
      showDialog: false,
      title: null,
      buffer: null,
      xkey: 1,
    };
  },
  methods: {
    createObject: function () {
      return createElement("embed", {
        domProps: {
          id: "fileViewer",
          style: "height: 100%; width: 100%",
        },
      });
    },
    show: async function (title, file) {
      // WADE - trying to create an embed manually, because the PDF is not refreshing
      //      this.$refs.fileViewerParent.appendChild(this.createObject);

      // document.getElementById("fileViewer").src = null;

      this.title = title;
      document.getElementById("fileViewer").src = file; // works better
      this.showDialog = true;
    },
    close: function () {
      this.title = "";
      document.getElementById("fileViewer").src = "xx";
      this.showDialog = false;
      this.xkey++;
    },
  },
};
</script>

<style>
.v-dialog {
  overflow-y: hidden;
}
.v-dialog .v-card__title,
.v-dialog .v-card__text,
.v-dialog .v-card__actions {
  padding-top: 5px;
  padding-bottom: 5px;
}
</style>