<template>
  <div>
    <v-container>
      <custom-snackbar ref="customSnackbar"></custom-snackbar>
      <v-card>
        <v-card-title
          class="headline font-weight-regular chsli-color white--text"
          >Open Audits - Practice/Hospital Department</v-card-title
        >
        <v-card-text>
          <v-form ref="form">
            <v-container fluid>
              <v-subheader>Audit Details</v-subheader>
              <v-divider></v-divider>
              <v-layout row wrap>
                <v-flex xs12 md4>
                  <v-text-field
                    v-model="practice0.practice_audit_id"
                    label="Audit ID"
                    disabled
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md4>
                  <v-text-field
                    v-model="auditorName"
                    label="Auditor"
                    disabled
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md4>
                  <v-text-field
                    v-model="practice0.audit_date"
                    label="Audit Date"
                    disabled
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md3>
                  <v-text-field
                    v-model="practice0.group_practice"
                    label="Practice"
                    disabled
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md3>
                  <v-text-field
                    v-model="practice0.group_name"
                    label="Group Name"
                    disabled
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md3>
                  <v-text-field
                    v-model="practice0.entity_name"
                    label="Facility"
                    disabled
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md3>
                  <v-text-field
                    v-model="practice0.tax_id"
                    label="Tax ID"
                    disabled
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md6>
                  <v-text-field
                    v-model="practice0.practice_director"
                    label="Practice Director"
                    disabled
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md3>
                  <v-select
                    v-model="practice0.practice_audit_type"
                    :items="auditTypes"
                    label="Audit Type"
                  ></v-select>
                </v-flex>
              </v-layout>

              <v-subheader>Audit Results</v-subheader>
              <v-divider></v-divider>
              <v-layout row wrap>
                <v-flex xs12 md6>
                  <v-menu
                    ref="menuDatePickerWorkflow"
                    :close-on-content-click="false"
                    v-model="menuDatePickerWorkflow"
                    :nudge-right="40"
                    :return-value.sync="practice0.workflow_sent_date"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                  >
                    <v-text-field
                      slot="activator"
                      v-model="practice0.workflow_sent_date"
                      label="Workflow Sent Date"
                      prepend-icon="event"
                      readonly
                      data-vv-name="Workflow Start Date"
                      :error-messages="errors.collect('Workflow Sent Date')"
                    ></v-text-field>
                    <v-date-picker
                      v-model="practice0.workflow_sent_date"
                      @input="
                        $refs.menuDatePickerWorkflow.save(
                          practice0.workflow_sent_date
                        )
                      "
                    ></v-date-picker>
                  </v-menu>
                </v-flex>
                <v-flex xs12 md6>
                  <v-menu
                    ref="menuDatePickerExecSummary"
                    :close-on-content-click="false"
                    v-model="menuDatePickerExecSummary"
                    :nudge-right="40"
                    :return-value.sync="practice0.executive_summary_sent_date"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                  >
                    <v-text-field
                      slot="activator"
                      v-model="practice0.executive_summary_sent_date"
                      label="Executive Summary Sent Date"
                      prepend-icon="event"
                      readonly
                      data-vv-name="Executive Summary Start Date"
                      :error-messages="
                        errors.collect('Executive Summary Sent Date')
                      "
                    ></v-text-field>
                    <v-date-picker
                      v-model="practice0.executive_summary_sent_date"
                      @input="
                        $refs.menuDatePickerExecSummary.save(
                          practice0.executive_summary_sent_date
                        )
                      "
                    ></v-date-picker>
                  </v-menu>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md6>
                  <v-menu
                    ref="menuDatePickerAnnouncement"
                    :close-on-content-click="false"
                    v-model="menuDatePickerAnnouncement"
                    :nudge-right="40"
                    :return-value.sync="practice0.announcement_sent_date"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                  >
                    <v-text-field
                      slot="activator"
                      v-model="practice0.announcement_sent_date"
                      label="Announcement Sent Date"
                      prepend-icon="event"
                      readonly
                      data-vv-name="Announcement Start Date"
                      :error-messages="errors.collect('Announcement Sent Date')"
                    ></v-text-field>
                    <v-date-picker
                      v-model="practice0.announcement_sent_date"
                      @input="
                        $refs.menuDatePickerAnnouncement.save(
                          practice0.announcement_sent_date
                        )
                      "
                    ></v-date-picker>
                  </v-menu>
                </v-flex>
                <v-flex xs12 md3>
                  <v-text-field
                    v-model="auditScore"
                    label="Audit Score"
                    disabled
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md3>
                  <v-text-field
                    v-model="auditRisk"
                    label="Audit Risk Level"
                    disabled
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md6>
                  <v-menu
                    ref="menuDatePickerAuditComplete"
                    :close-on-content-click="false"
                    v-model="menuDatePickerAuditComplete"
                    :nudge-right="40"
                    :return-value.sync="practice0.practice_audit_completed_date"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                  >
                    <v-text-field
                      slot="activator"
                      v-model="practice0.practice_audit_completed_date"
                      label="Audit Completed Date"
                      prepend-icon="event"
                      readonly
                      data-vv-name="Audit Completed Date"
                      :error-messages="errors.collect('Audit Completed Date')"
                    ></v-text-field>
                    <v-date-picker
                      v-model="practice0.practice_audit_completed_date"
                      @input="
                        $refs.menuDatePickerAuditComplete.save(
                          practice0.practice_audit_completed_date
                        )
                      "
                    ></v-date-picker>
                  </v-menu>
                </v-flex>
                <v-flex xs12 md6>
                  <v-text-field
                    v-model="practice0.pro_auditor_score"
                    label="Pro Auditor Score"
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12>
                  Please upload supporting documentation
                  <FileDragDrop
                    ref="fileDragDrop"
                    @fileList="updateFileList"
                    @fileClicked="previewFile"
                  />
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex>
                  <v-btn @click="saveAudit()">Save</v-btn>
                  <v-btn @click="cancelAudit()">Cancel</v-btn>
                  <v-btn v-if="isAdministrator" @click="deletePracticeAudit()"
                    >Delete</v-btn
                  >
                  <v-btn v-if="isAdministrator" @click="archiveAudit()"
                    >Archive</v-btn
                  >
                </v-flex>
              </v-layout>
              <v-subheader>Providers</v-subheader>
              <v-layout row wrap>
                <v-flex xs12>
                  <v-card class="mb-2">
                    <v-card-text>
                      <v-data-table
                        :headers="tableHeaders"
                        :items="practice"
                        class="evelation-1"
                        :pagination.sync="pagination"
                        dense
                      >
                        <template slot="items" slot-scope="{ item }">
                          <tr
                            @clickaa="
                              auditPhysician(item.npi, item.provider_audit_id)
                            "
                          >
                            <td>{{ item.status }}</td>
                            <td>{{ item.provider_name }}</td>
                            <td>{{ item.med_system }}</td>
                            <td>{{ item.billing_system }}</td>
                            <td>{{ item.audit_type }}</td>
                            <td>
                              {{
                                item.count_of_linked_rows == 0
                                  ? 0
                                  : item.in_progress
                              }}
                            </td>
                            <td>
                              {{
                                item.count_of_linked_rows == 0
                                  ? 0
                                  : item.complete
                              }}
                            </td>
                            <td>
                              <v-tooltip bottom>
                                <i
                                  class="material-icons"
                                  @click="
                                    auditPhysician(
                                      item.npi,
                                      item.provider_audit_id
                                    )
                                  "
                                  slot="activator"
                                  >edit</i
                                >
                                <span>View/Edit Encounter</span>
                              </v-tooltip>
                              <v-tooltip bottom v-if="isAdministrator">
                                <i
                                  class="material-icons"
                                  @click="
                                    deleteProviderAudit(
                                      item.provider_audit_id,
                                      $event
                                    )
                                  "
                                  slot="activator"
                                  >delete</i
                                >
                                <span
                                  >Delete Provider Audit and All Associated
                                  Encounters</span
                                >
                              </v-tooltip>
                            </td>
                          </tr>
                        </template>
                      </v-data-table>
                    </v-card-text>
                  </v-card>
                </v-flex>
              </v-layout>
            </v-container>
          </v-form>
        </v-card-text>
      </v-card>
    </v-container>
    <confirmation-dialog
      ref="confirmationDialog"
      :title="confirmation.title"
      :message="confirmation.message"
    ></confirmation-dialog>
  </div>
</template>

<script>
import Config from "../config";
import ConfirmationDialog from "@/components/ConfirmationDialog";
import CustomSnackbar from "@/components/Snackbar";
import FileDragDrop from "@/components/FileDragDrop";

import httpService from "../httpService";
import Router from "../router";

export default {
  name: "Main",
  components: {
    ConfirmationDialog,
    CustomSnackbar,
    FileDragDrop,
  },
  data: () => ({
    fileViewerDialog: false,
    menuDatePickerWorkflow: null,
    menuDatePickerExecSummary: null,
    menuDatePickerAnnouncement: null,
    menuDatePickerAuditComplete: null,
    local_practice_audit_id: null,
    tableHeaders: [
      { text: "Status", value: "status" },
      { text: "Provider", value: "provider_name" },
      { text: "Med System", value: "med_system" },
      { text: "Billing System", value: "billing_system" },
      { text: "Audit Type", value: "audit_type" },
      { text: "In Progress", value: "in_progress" },
      { text: "Completed", value: "complete" },
      { text: "", value: "adminDelete" },
    ],
    pagination: {
      rowsPerPage: 10,
    },
    practice: [
      {
        id: null,
        audit_provider_id: null,
        npi: null,
        provider_name: null,
        entity_name: null,
        audit_date: null,
        charge_correction_sent_date: null,
        audit_completed_date: null,
        no_charges_found: null,
        manually_selected_charges: null,
        audit_record_id: null,
        patient_name: null,
        service_date: null,
        education_audit_id: null,
        education_type: null,
        audit_type: null,
        education_date: null,
        cpt_billed: null,
        encounter_status: null,
        encounter_score: null,
        next_scheduled_audit_date: null,
        individual_summary_date: null,
        pro_auditor_score: null,
        files: [],
      },
    ],
    practiceScore: {
      deductions: 0,
      count_of_linked_rows: 0,
    },
    confirmation: {
      title: "",
      message: "",
    },
  }),
  created() {
    // work on this one record
    //this.audit = this.audits.find(elem => elem.audit_id == this.$route.params.audit_id);
    if (this.$route.params.practice_audit_id) {
      this.local_practice_audit_id = this.$route.params.practice_audit_id;
    }
    this.init();
  },
  computed: {
    practice0() {
      return this.practice[0];
    },
    currentUser() {
      return this.$store.getters.CURRENT_USER;
    },
    auditors() {
      return this.$store.getters.ALL_USERS;
    },
    auditorName() {
      if (this.practice0.auditor_id >= 0) {
        const indx = this.auditors.findIndex(
          (obj) => obj.id === this.practice0.auditor_id
        );
        return this.auditors[indx].fullname;
      }
    },
    auditTypes() {
      return this.$store.getters.APPLICATION_LISTS.filter(
        (row) => row.keyword === "AUDIT_TYPE"
      ).map((row) => row.value);
    },
    auditRisk() {
      return this.computeRisk(this.auditScore);
    },
    auditScore() {
      return this.computePracticeScore(
        this.practiceScore.deductions,
        this.practiceScore.count_of_linked_rows
      );
    },
    isAdministrator() {
      return this.currentUser.role === "administrator";
    },
  },
  methods: {
    async init() {
      console.log("initializing audit-practice..");
      await httpService.get(
        `${Config.apiRoot}/audit/practice/${this.local_practice_audit_id}`,
        (status, data) => {
          this.practice = data.message;

          //preset 2 fields for sorting     TODO - dont think we need to compute in-progress
          for (let i = 0; i < this.practice.length; i++) {
            this.practice[i].status = this.getStatus(this.practice[i]);
            this.practice[i].in_progress = this.calcIncomplete(
              this.practice[i]
            );
          }
          this.setPracticeScore(data.message);
          this.$refs.fileDragDrop.files = this.practice0.files;
        }
      );
    },
    saveAudit: function () {
      // Always update the 1st record for the physician audit data
      this.$validator.validateAll().then(async (result) => {
        if (!result) {
          return;
        } else {
          // this.practice0.audit_type = "cancelled"; // WADE not sure why this is here
          let formData = new FormData();
          for (let key in this.practice0) {
            if (key != "files") {
              formData.set(key, this.practice0[key]);
            }
          }
          // deleted blobs
          let tmpDel = [];
          for (var i = 0; i < this.practice0.files.length; i++) {
            if (
              this.practice0.files[i].deleted &&
              this.practice0.files[i].deleted == 1
            ) {
              tmpDel.push(this.practice0.files[i].id);
            }
          }
          formData.set("deletedBlobs", JSON.stringify(tmpDel));

          for (var i = 0; i < this.practice0.files.length; i++) {
            formData.append("files[" + i + "]", this.practice0.files[i]);
          }

          httpService.put(
            `${Config.apiRoot}/audit/practice/${this.practice0.practice_audit_id}`,
            formData,
            (status, data) => {
              if (data.success) {
                this.showMessage("success", "Record sucessfully saved");
                this.init(); // this refreshes the files
              } else {
                this.showMessage(
                  "error",
                  "There was a problem saving your changes.  MSG - " +
                    data.message.originalError.info.message
                );
              }
            }
          );
        }
      });
    },
    archiveAudit: function () {
      httpService.put(
        `${Config.apiRoot}/audit/practice/archive/${this.practice0.practice_audit_id}`,
        (status, data) => {
          if (data.success) {
            this.showMessage("success", "Record sucessfully saved");
          } else {
            this.showMessage(
              "error",
              "There was a problem saving your changes.  MSG - " +
                data.message.originalError.info.message
            );
          }
        }
      );
    },
    cancelAudit: function () {
      this.confirmation.title = "Cancel Audit?";
      this.confirmation.message =
        "Are you sure you want to cancel your changes?";
      this.$refs.confirmationDialog.confirmation().then((res) => {
        if (res) {
          this.$router.go(-1);
        }
      });
    },
    deleteProviderAudit: function (id) {
      this.confirmation.title = "Delete Provider's Audit?";
      this.confirmation.message =
        "Are you sure you want to delete this provider's audit?";
      this.$refs.confirmationDialog.confirmation().then((res) => {
        if (res) {
          httpService.delete(
            `${Config.apiRoot}/audit/provider/${id}`,
            (status, data) => {
              if (data.success) {
                //                this.showMessage("success", "Audit sucessfully deleted");
                this.popAudit(id);
              } else {
                this.showMessage(
                  "error",
                  "There was a problem deleting the audit.  MSG - " +
                    data.message.originalError.info.message
                );
              }
            }
          );
        }
      });
    },
    deletePracticeAudit: function () {
      this.confirmation.title = "Delete Practice Audit?";
      this.confirmation.message =
        "Are you sure you want to delete this practice audit?";
      this.$refs.confirmationDialog.confirmation().then((res) => {
        if (res) {
          httpService.delete(
            `${Config.apiRoot}/audit/practice/${this.local_practice_audit_id}`,
            (status, data) => {
              if (data.success) {
                this.showMessage(
                  "success",
                  "Practice audit sucessfully deleted"
                );
                this.$router.go(-1); // back to previous page
              } else {
                this.showMessage(
                  "error",
                  "There was a problem deleting the practice.  MSG - " +
                    data.message.originalError.info.message
                );
              }
            }
          );
        }
      });
    },
    auditPhysician: function (npi, provider_audit_id) {
      Router.push({
        name: "audit-provider",
        params: { npi: npi, provider_audit_id: provider_audit_id },
      });
    },
    showMessage: function (type, message) {
      this.$refs.customSnackbar.showSnackbar(message, type, 3000, true);
    },
    calcIncomplete(provider) {
      // incomplete returns 1 if there are no joined rows, check then adjust
      if (provider.count_of_linked_rows === 0) {
        return 0;
      }
      return provider.incomplete;
    },
    getStatus(provider) {
      if (provider.audit_completed_date) {
        return "Completed";
      }
      return "In Progress";
    },
    popAudit: function (id) {
      // remove item from the display list based on audit id
      this.practice.splice(
        this.practice.findIndex((item) => item.provider_audit_id == id),
        1
      );
    },
    setPracticeScore: function (source) {
      // compute score for entire practice
      this.practiceScore.deductions = 0;
      this.practiceScore.count_of_linked_rows = 0;
      for (let i = 0; i < source.length; i++) {
        if (source[i].count_of_linked_rows > 0) {
          this.practiceScore.deductions += 100 - source[i].current_audit_score;
          // this.practiceScore.count_of_linked_rows +=          source[i].count_of_linked_rows;
          this.practiceScore.count_of_linked_rows += 1;
        }
      }
    },
    updateFileList(obj) {
      // Updating parent file listing
      this.practice0.files = obj;
    },
    previewFile(key) {
      this.viewBlob(this.practice0.files[key].blob.data);
    },
  },
};
</script>

<style>
i {
  padding: 0 10px;
}
i:hover {
  cursor: pointer;
}
.small-font {
  font-size: 15px;
}
</style>
