<template>
  <div>
    <v-container>
      <custom-snackbar ref="customSnackbar"></custom-snackbar>
      <v-card class="mb-2">
        <v-card-title
          class="headline font-weight-regular chsli-color white--text"
        >Create Audit for Practice/Hospital Department</v-card-title>
        <v-card-text>
          <v-layout row wrap>
            <v-flex xs12 md2 pr-1>
              <v-select
                v-model="selectedEntityId"
                :items="entities"
                item-text="entity_name"
                item-value="id"
                label="Entity"
              ></v-select>
            </v-flex>
            <v-flex xs12 md6 pr-2>
              <v-autocomplete
                v-model="selectedPractice"
                :items="filteredPractices"
                persistent-hint
                item-text="group_practice"
                item-value="id"
                label="Practice/Hospital Department"
              ></v-autocomplete>
            </v-flex>
            <v-flex xs12 md2 pr-1>
              <v-select v-model="auditMonth" :items="months" label="Audit Month"></v-select>
            </v-flex>
            <v-flex xs12 md2>
              <v-select v-model="auditYear" :items="years" label="Audit Year"></v-select>
              <!--              <v-select
                v-model="selectedPracticeAuditId"
                :items="practiceAudits"
                label="Open Audits"
                item-value="id"
                item-text="created_date"
              ></v-select>
              -->
            </v-flex>
          </v-layout>
          <v-layout row wrap>
            <v-flex xs12>
              <v-checkbox v-model="allProviders" label="Create Audit for All Providers?"></v-checkbox>
            </v-flex>
          </v-layout>
          <v-layout row wrap>
            <v-flex xs12 md4 offset-md4>
              <v-btn block color="blue" @click="startPracticeAudit()">Start New Audit</v-btn>
            </v-flex>
          </v-layout>
        </v-card-text>
      </v-card>

      <v-card class="mb-2" v-if="selectedPractice && selectedPracticeAuditId">
        <!--  v-if="selectedPractice && selectedPracticeAuditId" OR v-if="false" -->
        <v-card-title
          class="headline font-weight-regular chsli-color white--text"
        >Provider Selection</v-card-title>
        <v-card-text>
          <v-layout row wrap>
            <v-flex xs12 md6 pr-2>
              <v-autocomplete
                v-model="selectedNpi"
                :items="physicianLookup"
                persistent-hint
                item-text="name"
                item-value="npi"
                label="Provider"
              ></v-autocomplete>
            </v-flex>
            <!--
            <v-flex xs12 md6>
              <v-select
                v-model="selectedPhysicianAuditId"
                :items="physicianAudits"
                label="Open Provider Audits"
                item-value="id"
                item-text="created_date"
              ></v-select>
            </v-flex>
            -->
          </v-layout>
          <v-layout row wrap>
            <v-flex xs12 md4 offset-md4>
              <v-btn
                block
                color="blue"
                :disabled="selectedNpi === null"
                @click="startPhysicianAudit()"
              >Start New Audit for Provider</v-btn>
            </v-flex>
          </v-layout>
        </v-card-text>
      </v-card>
      <patient-selection
        id="patientSelectionBlock"
        ref="PatientSelection"
        style="margin-top:10px;"
        v-on:created="init()"
      ></patient-selection>
    </v-container>
  </div>
</template>

<script>
import CustomSnackbar from "@/components/Snackbar";
import Config from "../config";
import httpService from "../httpService";
import PatientSelection from "@/components/PatientSelection";

// auditBeginDate: "2017-12-01",
// auditEndDate: "2018-11-15",
// auditPayer: "MEDICARE",
// selectedNpi: "1245218619",
// auditBeginDate: null,
// auditEndDate: null,
// auditPayer: null,
// selectedNpi: null,
//    physicianAudits: [],

export default {
  name: "Main",
  components: { CustomSnackbar, PatientSelection },
  data: () => ({
    allProviders: false,
    selectedEntityId: null,
    selectedPractice: null,
    selectedPracticeAuditId: null,
    selectedPhysicianAuditId: null,
    selectedNpi: null,
    months: [
      "01",
      "02",
      "03",
      "04",
      "05",
      "06",
      "07",
      "08",
      "09",
      "10",
      "11",
      "12"
    ],
    years: ["2019", "2020", "2021"]
  }),
  mounted() {},
  computed: {
    entities() {
      return this.$store.getters.ENTITY;
    },
    practices() {
      return this.$store.getters.PRACTICE;
    },
    physicians() {
      return this.$store.getters.PHYSICIAN.filter(
        p => p.physician_practice_id === this.selectedPractice
      );
    },
    physicianLookup() {
      return this.physicians
        .filter(row => row.billing != "N")
        .map(row => {
          let res = {};
          res.name = row.last_name + ", " + row.first_name;
          res.npi = row.npi;
          return res;
        });
    },
    filteredPractices() {
      return this.practices.filter(
        obj => obj.entity_id === this.selectedEntityId
      );
    }
  },
  methods: {
    showMessage: function(type, message) {
      this.$refs.customSnackbar.showSnackbar(message, type, 5000, true);
    },
    startPracticeAudit: function() {
      console.log("Creating practice audit");
      httpService.post(
        `${Config.apiRoot}/audit/practice/audit`,
        {
          practice: this.selectedPractice,
          auditDate: this.auditMonth.concat(this.auditYear),
          allProviders: this.allProviders
        },
        (status, data) => {
          if (!data.success) {
            this.showMessage(
              "error",
              "There was a problem creating the practice/Hospital Department audit records - " +
                data.message.originalError.info.message
            );
            return;
          }
          this.showMessage(
            "success",
            "Practice/Hospital Department Audit records created."
          );
          this.selectedNpi = null; // clear out the selected physician
          this.selectedPracticeAuditId = data.message.recordset[0].new_id;
        }
      );
    },
    startPhysicianAudit: function() {
      httpService.post(
        `${Config.apiRoot}/audit/physician/audit`,
        {
          physician: this.selectedNpi,
          audit_practice_id: this.selectedPracticeAuditId
        },
        (status, data) => {
          if (!data.success) {
            this.showMessage(
              "error",
              "There was a problem creating the Practice/Hospital Department audit records - " +
                data.message.originalError.info.message
            );
            return;
          }
          this.showMessage("success", "Physician Audit record created.");
          //          this.selectedPractice = null;
          this.selectedPhysicianAuditId = data.message.recordset[0].new_id;

          // user selected a record to work on
          this.$refs.PatientSelection.showComponent(
            this.selectedPhysicianAuditId,
            this.selectedNpi
          );

          // scroll to the new object
          document
            .getElementById("patientSelectionBlock")
            .scrollIntoView(false);
        }
      );
    }
  }
};
</script>

<style>
i:hover {
  cursor: pointer;
}

.record-selector .v-list__tile {
  height: auto;
}
</style>
