<template>
  <div>
    <v-container>
      <custom-snackbar ref="customSnackbar"></custom-snackbar>
      <v-card class="mb-2">
        <v-card-title
          class="headline font-weight-regular chsli-color white--text"
        >Practice/Hospital Department Maintenance</v-card-title>
        <v-card-text>
          <v-subheader class="pa-0">
            Practice Lookup
            <i
              class="material-icons"
              @click="newPractice()"
              style="padding-left:8px;"
            >add_box</i>
          </v-subheader>
          <v-layout row wrap>
            <v-flex xs12 md4 pr-1>
              <v-select
                v-model="selectedEntityId"
                :items="entities"
                item-text="entity_name"
                item-value="id"
                label="Entity"
              ></v-select>
            </v-flex>
            <v-flex xs12 md8>
              <v-autocomplete
                v-model="selectedId"
                :items="filteredPractices"
                persistent-hint
                item-text="group_practice"
                item-value="id"
                label="Practice"
                @change="selectPractice(selectedId)"
              ></v-autocomplete>
            </v-flex>
          </v-layout>
        </v-card-text>
      </v-card>
      <v-card v-if="showPracticeProfile">
        <v-card-title
          class="headline font-weight-regular chsli-color white--text"
        >Practice/Hospital Department Profile</v-card-title>
        <v-card-text>
          <v-form ref="form">
            <v-text-field v-model="practice.action" label="Action Type" v-show="false"></v-text-field>
            <v-container fluid>
              <v-layout row wrap>
                <v-flex xs12 md12>
                  <v-select
                    v-model="practice.entity_id"
                    :items="entities"
                    item-text="entity_name"
                    item-value="id"
                    label="Entity"
                  ></v-select>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md12>
                  <v-text-field
                    v-model="practice.group_practice"
                    v-validate="'required'"
                    data-vv-name="Group Practice"
                    label="Group Practice"
                    required
                    :error-messages="errors.collect('Group Practice')"
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md12>
                  <v-text-field
                    v-model="practice.group_name"
                    v-validate="'required'"
                    data-vv-name="Group Name"
                    label="Group Name"
                    required
                    :error-messages="errors.collect('Group Name')"
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md3>
                  <v-text-field
                    v-model="practice.contact1_name"
                    data-vv-name="Contact1 Name"
                    label="Contact1 Name"
                    :error-messages="errors.collect('Contact1 Name')"
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md3>
                  <v-text-field
                    v-model="practice.contact1_phone"
                    data-vv-name="Contact1 Phone"
                    label="Contact1 Phone"
                    :error-messages="errors.collect('Contact1 Phone')"
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md6>
                  <v-text-field
                    v-model="practice.contact1_email"
                    data-vv-name="Contact1 Email"
                    label="Contact1 Email"
                    :error-messages="errors.collect('Contact1 Email')"
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md3>
                  <v-text-field
                    v-model="practice.contact2_name"
                    data-vv-name="Contact2 Name"
                    label="Contact2 Name"
                    :error-messages="errors.collect('Contact2 Name')"
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md3>
                  <v-text-field
                    v-model="practice.contact2_phone"
                    data-vv-name="Contact2 Phone"
                    label="Contact2 Phone"
                    :error-messages="errors.collect('Contact2 Phone')"
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md6>
                  <v-text-field
                    v-model="practice.contact2_email"
                    data-vv-name="Contact2 Email"
                    label="Contact2 Email"
                    :error-messages="errors.collect('Contact2 Email')"
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md3>
                  <v-text-field
                    v-model="practice.contact3_name"
                    data-vv-name="Contact3 Name"
                    label="Contact3 Name"
                    :error-messages="errors.collect('Contact3 Name')"
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md3>
                  <v-text-field
                    v-model="practice.contact3_phone"
                    data-vv-name="Contact3 Phone"
                    label="Contact3 Phone"
                    :error-messages="errors.collect('Contact3 Phone')"
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md6>
                  <v-text-field
                    v-model="practice.contact3_email"
                    data-vv-name="Contact3 Email"
                    label="Contact3 Email"
                    :error-messages="errors.collect('Contact3 Email')"
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md12>
                  <v-text-field
                    v-model="practice.practice_director"
                    data-vv-name="Practice Director"
                    label="Practice Director"
                    :error-messages="errors.collect('Practice Director')"
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md6>
                  <v-text-field
                    v-model="practice.tax_id"
                    data-vv-name="Tax ID"
                    label="Tax ID"
                    :error-messages="errors.collect('Tax ID')"
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md6>
                  <v-select
                    v-model="practice.auditor_id"
                    :items="auditors"
                    item-text="fullname"
                    item-value="id"
                    label="Auditor"
                  ></v-select>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 sm8 md4>
                  <v-btn @click="savePractice()">Save</v-btn>
                  <v-btn @click="cancelPractice()">Cancel</v-btn>
                </v-flex>
                <v-flex xs12 sm4 md2>
                  <v-btn @click="deletePractice()">Delete</v-btn>
                </v-flex>
              </v-layout>
            </v-container>
          </v-form>
        </v-card-text>
      </v-card>
    </v-container>
    <confirmation-dialog
      ref="confirmationDialog"
      :title="confirmComponentMessage.title"
      :message="confirmComponentMessage.message"
    ></confirmation-dialog>

    <!-- <pre>{{ currentUser }}</pre>
    <pre>{{ audits }}</pre>-->
  </div>
</template>

<script>
import ConfirmationDialog from "@/components/ConfirmationDialog";
import CustomSnackbar from "@/components/Snackbar";

const initializePractice = () => {
  return {
    id: "",
    group_name: "",
    group_practice: "",
    contact1_name: "",
    contact1_phone: "",
    contact1_email: "",
    contact2_name: "",
    contact2_phone: "",
    contact2_email: "",
    contact3_name: "",
    contact3_phone: "",
    contact3_email: "",
    auditor_id: -1,
    tax_id: "",
    practice_director: "",
    entity_id: "",
  };
};

export default {
  name: "Main",
  components: {
    ConfirmationDialog,
    CustomSnackbar,
  },
  data: () => ({
    practice: {},
    selectedId: null,
    selectedEntityId: null,
    showPracticeProfile: false,
    confirmComponentMessage: { title: "", message: "" },
  }),
  computed: {
    entities() {
      return this.$store.getters.ENTITY;
    },
    practices() {
      return this.$store.getters.PRACTICE;
    },
    auditors() {
      return this.$store.getters.ALL_USERS;
    },
    filteredPractices() {
      return this.practices.filter(
        (obj) => obj.entity_id === this.selectedEntityId
      );
    },
  },
  methods: {
    selectPractice: function (id) {
      if (id) {
        this.practice = Object.assign(
          {},
          this.practices.find((p) => p.id === id)
        );
        this.practice.action = "update"; // updating an existing practice
        this.showPracticeProfile = true;
      }
    },
    newPractice: function () {
      this.practice = initializePractice();
      this.practice.action = "new";
      this.showPracticeProfile = true;
    },
    savePractice: function () {
      this.$validator.validateAll().then((result) => {
        if (!result) {
          return false;
        } else {
          let dispatchType = "";
          if (this.practice.action === "new") {
            dispatchType = "NEW_PRACTICE";
          } else {
            dispatchType = "UPDATE_PRACTICE";
          }
          this.$store
            .dispatch(dispatchType, this.practice)
            .then((data) => {
              if (data === true) {
                this.showMessage("success", "Record sucessfully saved");
                if (this.practice.action === "new") {
                  this.showPracticeProfile = false;
                  this.practice = initializePractice(); // re-initialize fields
                  this.$store.dispatch("FETCH_PRACTICE"); // updated store data
                }
              } else {
                this.showMessage(
                  "error",
                  "There was a problem saving your changes.  MSG - " + data
                );
              }
            })
            .catch((err) => {
              console.log("err - ", err);
              this.showMessage(
                "error",
                "There was a problem saving your changes.  MSG - " + err
              );
            });
        }
      });
    },
    deletePractice: function () {
      this.confirmComponentMessage.title = "Confirm Deletion?";
      this.confirmComponentMessage.message =
        "Are you sure you want to delete this record?";
      this.$refs.confirmationDialog
        .confirmation()
        .then((res) => {
          if (res) {
            this.showPracticeProfile = false;
            this.$store
              .dispatch("DELETE_PRACTICE", this.practice)
              .then((data) => {
                if (data === true) {
                  this.showMessage("success", "Record sucessfully deleted");
                  this.selectedId = null;
                  this.selectedEntityId = null;
                  this.showPracticeProfile = false;
                } else {
                  this.showMessage(
                    "error",
                    "There was a problem deleting the record.  MSG - " + data
                  );
                }
              });
          }
        })
        .catch((err) => {
          console.log("err - ", err);
          this.showMessage(
            "error",
            "There was a problem sdeleting the record.  MSG - " + err
          );
        });
    },
    cancelPractice: function () {
      this.confirmComponentMessage.title = "Cancel?";
      this.confirmComponentMessage.message =
        "Are you sure you want to cancel your changes?";
      this.$refs.confirmationDialog.confirmation().then((res) => {
        if (res) {
          this.selectedId = null;
          this.selectedEntityId = null;
          this.showPracticeProfile = false;
        }
      });
    },
    showMessage: function (type, message) {
      this.$refs.customSnackbar.showSnackbar(message, type, 3000, false);
    },
  },
};
</script>

<style>
i:hover {
  cursor: pointer;
}
</style>
