<template>
  <div>
    <v-container>
      <custom-snackbar ref="customSnackbar"></custom-snackbar>
      <v-card>
        <v-card-title class="headline font-weight-regular chsli-color white--text">Provider</v-card-title>
        <v-card-text>
          <v-form ref="form">
            <v-container fluid>
              <v-subheader>Audit Details</v-subheader>
              <v-divider></v-divider>
              <v-layout row wrap>
                <v-flex xs12 md4>
                  <v-text-field v-model="audit.id" label="Audit ID" disabled></v-text-field>
                </v-flex>
                <v-flex xs12 md4>
                  <v-text-field v-model="audit.auditor" label="Auditor"></v-text-field>
                </v-flex>
                <v-flex xs12 md4>
                  <v-text-field v-model="audit.audit_date" label="Audit Date"></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md4 style="display: flex;">
                  <v-text-field v-model="audit.provider_name" label="Provider Name"></v-text-field>
                  <a
                    v-if="audit.provider_email"
                    :href="`mailto:${audit.provider_email}`"
                    style="margin:auto;"
                  >
                    <i class="material-icons">email</i>
                  </a>
                </v-flex>
                <v-flex xs12 md2>
                  <v-text-field v-model="audit.npi" label="Provider NPI"></v-text-field>
                </v-flex>
                <v-flex xs12 md2>
                  <v-text-field v-model="audit.entity_name" label="Facility"></v-text-field>
                </v-flex>
                <v-flex xs12 md4>
                  <v-select v-model="audit.audit_type" :items="auditTypes" label="Audit Type"></v-select>
                </v-flex>
              </v-layout>
              <v-subheader>Audit Results</v-subheader>
              <v-divider></v-divider>
              <v-layout row wrap>
                <v-flex xs12 md6>
                  <v-checkbox v-model="audit.no_charges_found" label="No Charges Found"></v-checkbox>
                </v-flex>
                <v-flex xs12 md6>
                  <v-checkbox
                    v-model="audit.manually_selected_charges"
                    label="Manually Selected Charges"
                  ></v-checkbox>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md6>
                  <v-menu
                    ref="menuDatePickerChargeCorrection"
                    :close-on-content-click="false"
                    v-model="menuDatePickerChargeCorrection"
                    :nudge-right="40"
                    :return-value.sync="audit.charge_correction_start_date"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                  >
                    <v-text-field
                      slot="activator"
                      v-model="audit.charge_correction_start_date"
                      label="Charge Correction Start Date"
                      prepend-icon="event"
                      readonly
                      data-vv-name="Charge Correction Start Date"
                      :error-messages="errors.collect('Charge Correction Start Date')"
                    ></v-text-field>
                    <v-date-picker
                      v-model="audit.charge_correction_start_date"
                      @input="$refs.menuDatePickerChargeCorrection.save(audit.charge_correction_start_date)"
                    ></v-date-picker>
                  </v-menu>
                </v-flex>
                <v-flex xs12 md6>
                  <v-layout>
                    <v-flex xs12 md4 pr-1>
                      <v-text-field v-model="audit.education_type" label="Communication Type"></v-text-field>
                    </v-flex>
                    <v-flex xs12 md5 pr-1>
                      <v-text-field
                        v-model="audit.education_date"
                        label="Results Communicated Date"
                      ></v-text-field>
                    </v-flex>
                    <v-flex xs12 md3>
                      <v-btn @click="showEducation()">Education ></v-btn>
                    </v-flex>
                  </v-layout>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md6>
                  <v-menu
                    ref="menuDatePickerAuditComplete"
                    :close-on-content-click="false"
                    v-model="menuDatePickerAuditComplete"
                    :nudge-right="40"
                    :return-value.sync="audit.audit_completed_date"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                  >
                    <v-text-field
                      slot="activator"
                      v-model="audit.audit_completed_date"
                      label="Audit Completed Date"
                      prepend-icon="event"
                      readonly
                      data-vv-name="Audit Completed Date"
                      :error-messages="errors.collect('Audit Completed Date')"
                    ></v-text-field>
                    <v-date-picker
                      v-model="audit.audit_completed_date"
                      @input="$refs.menuDatePickerAuditComplete.save(audit.audit_completed_date)"
                    ></v-date-picker>
                  </v-menu>
                </v-flex>
                <v-flex xs12 md6>
                  <v-text-field v-model="auditScore" label="Audit Score"></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex>
                  <v-btn @click="saveAudit()">Save</v-btn>
                  <v-btn @click="cancelAudit()">Cancel</v-btn>
                </v-flex>
              </v-layout>
              <v-subheader>
                Encounters
                <v-tooltip bottom>
                  <i
                    class="material-icons"
                    @click="addEncounter()"
                    slot="activator"
                    style="color:#000000"
                  >add_box</i>
                  <span>Create Additional Encounters</span>
                </v-tooltip>
              </v-subheader>
              <v-divider></v-divider>
              <v-layout row wrap>
                <v-flex xs12>
                  <v-list>
                    <v-list-tile-title>
                      <v-layout>
                        <v-flex md3>Patient Name</v-flex>
                        <v-flex md3>CPT</v-flex>
                        <v-flex md2>Date of Service</v-flex>
                        <v-flex md2>Status</v-flex>
                        <v-flex md2></v-flex>
                      </v-layout>
                    </v-list-tile-title>
                    <template v-for="(encounter, index) in audit">
                      <v-list-tile v-if="encounter.patient_name" :key="index">
                        <v-layout>
                          <v-flex md3>{{ encounter.patient_name }}</v-flex>
                          <v-flex md3>{{ encounter.cpt_billed }}</v-flex>
                          <v-flex md2>{{ encounter.service_date }}</v-flex>
                          <v-flex md2 v-if="!encounter.encounter_status.includes('X')">Complete</v-flex>
                          <v-flex md2 v-else>Pending</v-flex>
                          <v-flex md2>
                            <v-tooltip bottom>
                              <i
                                class="material-icons"
                                @click="auditEncounter(encounter.audit_record_id)"
                                slot="activator"
                              >edit</i>
                              <span>View/Edit Encounter</span>
                            </v-tooltip>
                            <v-tooltip bottom>
                              <i
                                class="material-icons"
                                @click="deleteEncounter(encounter.audit_record_id, index)"
                                slot="activator"
                              >delete</i>
                              <span>Delete Encounter</span>
                            </v-tooltip>
                          </v-flex>
                        </v-layout>
                      </v-list-tile>
                    </template>
                    <v-list-tile v-if="!audit.patient_name">
                      <v-layout>
                        <v-flex md12>None</v-flex>
                      </v-layout>
                    </v-list-tile>
                  </v-list>
                </v-flex>
              </v-layout>
            </v-container>
          </v-form>
        </v-card-text>
      </v-card>
    </v-container>
  </div>
</template>

<script>
import CustomSnackbar from "@/components/Snackbar";
import Config from "../config";
import httpService from "../httpService";
import PatientSelection from "@/components/PatientSelection";
import { ValidationProvider } from "vee-validate";

export default {
  name: "Main",
  components: { CustomSnackbar },
  data: () => ({
    audit: {
      physician_audit_id: -1,
      practice_audit_id: -1 // use -1 for physicians with no practice?  WADE
    },
    selectedEntityId: null,
    selectedPractice: null,
    selectedPhysicianAuditId: null,
    months: [
      "01",
      "02",
      "03",
      "04",
      "05",
      "06",
      "07",
      "08",
      "09",
      "10",
      "11",
      "12"
    ],
    years: ["2019", "2020", "2021"]
  }),
  mounted() {},
  computed: {
    entities() {
      return this.$store.getters.ENTITY;
    },
    practices() {
      return this.$store.getters.PRACTICE;
    },
    physicians() {
      return this.$store.getters.PHYSICIAN.filter(
        p => p.physician_practice_id === this.selectedPractice
      );
    },
    physicianLookup() {
      return this.physicians
        .filter(row => row.billing != "N")
        .map(row => {
          let res = {};
          res.name = row.last_name + ", " + row.first_name;
          res.npi = row.npi;
          return res;
        });
    },
    filteredPractices() {
      return this.practices.filter(
        obj => obj.entity_id === this.selectedEntityId
      );
    }
  },
  methods: {
    showMessage: function(type, message) {
      this.$refs.customSnackbar.showSnackbar(message, type, 5000, true);
    },
    saveAudit: function() {
      // // TODO
      // create new provider
      // create new provider audit - what practice audit do I assign to?
      // add encounters manually and assign it to the audit
      // create encounter detail record

      // create the physician record
      httpService.post(
        `${Config.apiRoot}/physician`,
        {
          audit: this.audit,
          user_role: "administrator"
        },
        (status, data) => {
          if (!data.success) {
            this.showMessage(
              "error",
              "There was a problem creating the record - " +
                data.message.originalError.info.message
            );
            return;
          }
          this.showMessage("success", "Record created.");
          this.selectedPhysicianAuditId = data.message.recordset[0].new_id;
        }
      );
    }
  }
};
</script>

<style>
i:hover {
  cursor: pointer;
}

.record-selector .v-list__tile {
  height: auto;
}
</style>
